/* ==========================================================================
Crossfade Images shortcode styles - start
========================================================================== */
.qode-crossfade-images {
	display: inline-block;
	position: relative;

	.qode-cfi-link {
		position: absolute;
		top:0;
		left:0;
		height: 100%;
		width: 100%;
		z-index: 100;
	}

	.qode-cfi-img-holder {
		@include qodeBoxShadow(0px 10px 18px 0px rgba(0,0,0,0.08));
		@include qodeTransition(all .3s);

		.qode-cfi-img-holder-inner {
			position: relative;
			overflow: hidden;

			img {
				vertical-align: middle;
				@include qodeTransition(all .3s ease);
			}

			.qodef-cfi-image-hover{
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 100%;
				z-index: 10;
				background-size: cover;
				background-position: center center;
				background-repeat: no-repeat;
				opacity: 0;
				@include qodeTransform(scale(1.3));
				@include qodeTransition(all .3s ease);
			}
		}
	}

	.qode-cfi-title-holder {
		text-align: center;
		margin: 30px 0;

		.qode-cfi-title {
			text-transform: none;
			font-weight: 400;
			font-size: 19px;
		}
	}

	&:hover {
		.qode-cfi-img-holder {
			@include qodeBoxShadow(0px 10px 18px 1px rgba(0,0,0,0.2));

			.qode-cfi-img-holder-inner {
				img {
					@include qodeTransform(scale(.8));
					opacity: 0;
				}
				.qodef-cfi-image-hover{
					opacity: 1;
					@include qodeTransform(scale(1));
					-webkit-transition: -webkit-transform .3s .05s, opacity .3s .05s;
					transition: transform .3s .05s, opacity .3s .05s;
				}
			}
		}
	}
}
/* ==========================================================================
Crossfade Images shortcode styles - end
========================================================================== */