/* ==========================================================================
# Blog Carousel Titled style - begin
========================================================================== */
.qode-blog-carousel-titled {
	$bct-border-color: rgba(0,0,0,0.07);

	background-color: #fff;
	opacity: 0;
	@include qodeTransition(opacity 0.2s ease-out);


	.qode-bct-title-holder {
		position: relative;
		display: table;
		width: 100%;
		box-sizing: border-box;
		background-color: $first-main-color;
		border-bottom: 1px solid $bct-border-color;
		color: #fff;
		padding: 0 56px;
		height: 56px;

		* {
			color: inherit;
		}

		> * {
			display: table-cell;
			vertical-align: middle;
			text-align: center
		}
		.qode-bct-caroufredsel-nav-holder {
			width: 56px;
		}
		.qode-bct-caroufredsel-prev,
		.qode-bct-caroufredsel-next {
			position: absolute;
			width: 56px;
			top: 0;
			height: 100%;

			.qode-bct-caroufredsel-nav-inner {
				display: table;
				width: 100%;
				height: 100%;
			}

			.qode-bct-caroufredsel-nav-icon-holder {
				display: table-cell;
				vertical-align: middle;
				font-size: 33px;
			}
		}

		.qode-bct-caroufredsel-prev {
			left: 0;
			border-right: 1px solid $bct-border-color;
		}

		.qode-bct-caroufredsel-next {
			right: 0;
			border-left: 1px solid $bct-border-color;
		}
	}
	.qode-bct-posts-holder {
		padding: 32px 17px 44px;
	}
	.qode-bct-post {
		background-color: transparent;
		position: relative;
		box-sizing: border-box;
		margin: 0;
		float: left;
		padding: 0 17px;

		.qode-bct-post-image {
			img {
				vertical-align: middle;
			}
		}

		.qode-bct-post-title {
			margin-bottom: 14px;
		}

		.qode-bct-post-text {
			padding: 26px 0 0 0;
		}

		.qode-bct-post-excerpt {
			margin: 0;
			padding: 0;
		}

		.qode-bct-post-date {
			margin-top: 15px;
			color: $first-main-color;
		}
	}

}
/* ==========================================================================
# Blog Carousel Titled - end
========================================================================== */