.qode-simple-quote-holder{
	
	position: relative;
	margin-top: 50px;

	.qode-simple-quote-triangle {
	    width: 0;
	    height: 0;
	    border-bottom: 50px solid #fff;
	    border-right: 50px solid transparent;
	    z-index: 10;
	    position: absolute;
	    left: 8%;
	    top: -50px;
	}

	.qode-simple-quote-holder-inner{
		
		display: table;
		width: 100%;
		position: relative;
		background-color: #fff;
		box-sizing: border-box;
		padding: 35px 45px;
		border-radius: 5px;

		.qode-simple-quote-icon-holder{
			display: table-cell;
			width: 165px;
			vertical-align: middle;
			fill: $first-main-color;

			svg{
				g{
					fill: inherit;
				}
			}
		}

		.qode-simple-quote-content-holder{
			display: table-cell;
			vertical-align: middle;

			.qode-simple-quote-text-holder{
				margin-bottom: 10px;
			}
		}
	}

	&.qode-simple-quote-enabled-shadow{
		
		.qode-simple-quote-triangle-shadow {
			width: 0;
			height: 0;
			border-bottom: 50px solid  rgba(0,0,0,.05);
			border-right: 50px solid transparent;
			filter: blur(19px);
			position: absolute;
			left: 8%;
			top: -50px;
		}

		.qode-simple-quote-holder-inner{
			@include qodeBoxShadow(0 0 25px 10px rgba(0,0,0,.02));
		}
	}

}