/* Content Slider - begin
========================================================================== */
.qode-banner{
    overflow: hidden;
    position: relative;

	.qode-banner-image {
		img {
			vertical-align: middle;
		}
	}

	.qode-banner-link{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 11;
	}

    .qode-banner-content{
		position: absolute;
		top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	    z-index: 10;
	    padding: 7.2%;
	    box-sizing: border-box;
	    background-color: rgba(0, 0, 0 , 0.3);
	    @include qodeTransition(opacity 0.3s ease-out);

	    .qode-banner-content-inner {

		    display: table;
		    width: 100%;
		    height: 100%;
	    }

	    .qode-banner-text-holder {
		    display: table-cell;
		    vertical-align: middle;
		    text-align: center;

		    * {
			    color: #fff;
		    }

	    }
    }

	&.qode-banner-va-bottom {
		.qode-banner-content {
			.qode-banner-text-holder {
				vertical-align: bottom;
			}
		}
	}

	&.qode-banner-va-top {
		.qode-banner-content {
			.qode-banner-text-holder {
				vertical-align: top;
			}
		}
	}

	&:hover {
		.qode-banner-content {
			opacity: 0;
		}
	}

}


/* Content Slider - end
========================================================================== */