/* ==========================================================================
   Interactive Icon Showcase start styles
   ========================================================================== */

.qode-int-icon-showcase{
	width: 100%;
	height: 0;
	padding-top: 100%;
	position: relative;

	.qode-int-icon-showcase-inner{
		position: absolute;
		top: 0;
		left: 0;
		width: calc(100% - 100px);
		height: calc(100% - 100px);
		margin: 50px;
	}

	.qode-int-icon-circle{
		position: absolute;
		top: 0;
		left: 0;
		width: calc(100% - 100px);
		height: calc(100% - 100px);
		margin: 50px;
		background-color: transparent;
		border: 2px solid #fff;
		@include qodeBorderRadius(50%);
		@include qodeTransition(all .85s cubic-bezier(0.18, 0.89, 0.32, 1.28));
		@include qodeTransform(scale(.1,.1));
		.touchevents .no_animation_on_touch  & {
			@include qodeTransition(none);
			@include qodeTransform(scale(1,1));
		}
	}

	&.qode-appeared{
		.qode-int-icon-circle{
			@include qodeTransform(scale(1,1));
		}
	}

	.qode-showcase-content{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		text-align: center;
		@include qodeTransition(opacity .3s ease-out);

		.qode-section-subtitle{
			text-transform: capitalize;
		}

		.qode-showcase-content-inner{
			margin-top: 18px;
		}
	}

	.qode-showcase-content-table{
		display: table;
		height: 100%;
		width: 100%;
		table-layout: fixed;
	}


	.qode-showcase-content-cell{
		display: table-cell;
		vertical-align: middle;
		padding: 0 20%;
	}
}

.qode-showcase-icon{
	position: absolute;
	z-index: 10;
	cursor: pointer;
	-webkit-tap-highlight-color: transparent;
	@include qodeTransform(translate(-50%,-50%) scale(1.01));

	.touchevents .no_animation_on_touch .qode-int-icon-showcase & {
		-webkit-transition:opacity .2s ease !important;
		transition:opacity .2s ease !important;
		-webkit-transition-delay: 0s !important;
		transition-delay: 0s !important;
	}
	.qode-icon-holder.qode-icon-circle{
		background-color: #fff;

		.qode-icon-element {
			color: $first-main-color;
		}
	}
	.qode-icon-holder{
		width: 80px;
		height: 80px;
		line-height: 80px;
		font-size: 38px;
	}
}

.qode-showcase-item-holder{

	&.qode-showcase-active{
		.qode-icon-holder.qode-icon-circle{
			background-color: $first-main-color !important; //because of the inline color

			.qode-icon-element{
				color: #fff;
			}

		}


		.qode-showcase-content{
			opacity: 1;
		}
	}

	.qode-showcase-icon {
		top:50%;
		left:50%;
		opacity: 0;
		@include qodeTransform(translate(-50%,-50%));
		-webkit-transition:opacity .2s ease, top .5s cubic-bezier(0.18, 0.89, 0.32, 1), left .5s cubic-bezier(0.18, 0.89, 0.32, 1), -webkit-transform .5s ease;
		transition:opacity .2s ease, top .5s cubic-bezier(0.18, 0.89, 0.32, 1), left .5s cubic-bezier(0.18, 0.89, 0.32, 1), transform .5s ease;
	}

	.qode-int-icon-showcase.qode-appeared &,
	.touchevents .no_animation_on_touch .qode-int-icon-showcase & {
		.qode-showcase-icon{
			opacity: 1;
		}
		&:first-child .qode-showcase-icon{
			top: 0;
			left: 50%;
			-webkit-transition-delay:.65s;
			transition-delay:.65s;
		}
		&:nth-child(2) .qode-showcase-icon{
			top: 14.6%;
			left: 85.4%;
			-webkit-transition-delay:.85s;
			transition-delay:.85s;
		}

		&:nth-child(3) .qode-showcase-icon{
			top: 50%;
			left: 100%;
			-webkit-transition-delay: 1.05s;
			transition-delay: 1.05s;
		}

		&:nth-child(4) .qode-showcase-icon{
			top: 85.4%;
			left: 85.4%;
			-webkit-transition-delay: 1.25s;
			transition-delay: 1.25s;
		}

		&:nth-child(5) .qode-showcase-icon{
			top: 100%;
			left: 50%;
			-webkit-transition-delay:1.45s;
			transition-delay:1.45s;
		}

		&:nth-child(6) .qode-showcase-icon{
			top: 85.4%;
			left: 14.6%;
			-webkit-transition-delay:1.65s;
			transition-delay:1.65s;
		}

		&:nth-child(7) .qode-showcase-icon{
			top: 50%;
			left: 0;
			-webkit-transition-delay:1.85s;
			transition-delay:1.85s;
		}

		&:nth-child(8) .qode-showcase-icon{
			top: 14.6%;
			left: 14.6%;
			-webkit-transition-delay:2.05s;
			transition-delay:2.05s;
		}
	}
}
