.qode-comparison-slider {
    img {
        position: absolute;
    }

    visibility: hidden;

    &.active {
        cursor: -webkit-grab;
        cursor: grab;
    }

    &.qode-comparison-slider-with-frame {
        .qode-comparison-slider-frame-holder {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            img {
                clip: initial !important;
            }
        }

        padding-bottom: 30px;

        .twentytwenty-before{
            z-index: 1;
        }

        .twentytwenty-handle:before,
        .twentytwenty-handle:after{
            display: none;
        }

    }

    color: #fff;

    &.qode-comparison-slider-dark{
        color: #000;
    }
}

.twentytwenty-horizontal{
    .qode-comparison-slider{
        &.qode-comparison-slider-with-frame{
            .twentytwenty-handle{
                margin-top: -67.5px;
            }
        }
    }
}

.twentytwenty-horizontal{
    .qode-comparison-slider{
        &.qode-comparison-slider-with-frame{
            @include ipad-portrait {
                .twentytwenty-handle {
                    margin-top: -50px;
                }
            }
            @include phone-portrait{
                .twentytwenty-handle {
                    margin-top: -40px;
                }
            }
        }
    }
}

.qode-comparison-slider-with-frame.twentytwenty-container{
    img{
        width: auto;
    }

    .qode-comparison-slider-frame-holder{
        img{
            width: 100%;
        }
    }
}

.twentytwenty-horizontal .twentytwenty-handle:before, .twentytwenty-horizontal .twentytwenty-handle:after, .twentytwenty-vertical .twentytwenty-handle:before, .twentytwenty-vertical .twentytwenty-handle:after {
    display: none;
}

.twentytwenty-horizontal .twentytwenty-handle:before, .twentytwenty-horizontal .twentytwenty-handle:after {
    display: none;
}

.twentytwenty-vertical .twentytwenty-handle:before, .twentytwenty-vertical .twentytwenty-handle:after {
    display: none;
}

.twentytwenty-before-label, .twentytwenty-after-label {
    display: none;
}

.twentytwenty-container {
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
    z-index: 0;
    overflow: hidden;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;

    img {
        max-width: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        display: block;
    }
}

.twentytwenty-container * {
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}

.twentytwenty-before {
    z-index: 20;
}

.twentytwenty-after {
    z-index: 10;
}

.twentytwenty-handle {
    height: 50px;
    width: 80px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -40px;
    margin-top: -40px;
    z-index: 40;
    cursor: -webkit-grab;
    cursor: grab;
}

.twentytwenty-vertical{
    .twentytwenty-handle{
        height: 80px;
    }
}


.twentytwenty-handle:before {
    .twentytwenty-vertical & {
        left: 50%;
        margin-left: 43px;
    }

    .twentytwenty-horizontal & {
        bottom: 50%;
        margin-bottom: 43px;
    }
}

.twentytwenty-handle:after {
    .twentytwenty-vertical & {
        right: 50%;
        margin-right: 43px;
    }

    .twentytwenty-horizontal & {
        top: 50%;
        margin-top: 43px;
    }
}

.twentytwenty-left-arrow, .twentytwenty-right-arrow, .twentytwenty-down-arrow, .twentytwenty-up-arrow {
    display: inline-block;
    height: 50px;
    width: 40px;
    line-height: 86px;
    font-size: 36px;
    text-align: center;
    position: relative;
}

.twentytwenty-down-arrow, .twentytwenty-up-arrow{
    display: block;
}

.twentytwenty-horizontal,
.twentytwenty-vertical{
    .twentytwenty-left-arrow,
    .twentytwenty-right-arrow,
    .twentytwenty-down-arrow,
    .twentytwenty-up-arrow{
        &:before {
            font-family: 'ElegantIcons';
            font-size: 44px;
            position: absolute;
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            line-height: 50px;
        }
    }
}

.twentytwenty-horizontal .twentytwenty-left-arrow {
    &:before {
        content: '\44';
        left: 7px;
    }
}

.twentytwenty-horizontal .twentytwenty-right-arrow {
    &:before {
        content: '\45';
        left: -11px;
    }
}

.twentytwenty-vertical .twentytwenty-down-arrow {
    &:before {
        content: '\42';
        top: 10px;
    }
}

.twentytwenty-vertical .twentytwenty-up-arrow {
    &:before {
        content: '\43';
        top: -8px;
    }
}

.twentytwenty-vertical{
    .twentytwenty-down-arrow,
    .twentytwenty-up-arrow{

        height: 40px;
        width: 80px;

        &:before {
            line-height: 40px;
        }
    }
}