/* ==========================================================================
   #Info Card With Icon style - start
   ========================================================================== */
.qode-info-card-with-icon{

	text-align: center;

	.qode-icwi-image {
		img {
			vertical-align: middle;
		}
	}

	.qode-icwi-image-icon-holder {

		position: relative;

		.qode-icwi-icon-holder {
			position: absolute;
			text-align: center;
			left: 0;
			top: 50%;
			@include qodeTransform(translateY(-50%));
			width: 100%;
		}
	}
	.qode-icwi-text-holder {
		padding: 32px 20px 37px;
		background-color: #fff;
	}

	.qode-icwi-text {
		margin-top: 20px;
	}
}
/* ==========================================================================
   #Image with icon and text style - end
   ========================================================================== */