/* ==========================================================================
   Pricing Calculator shortcode start styles
   ========================================================================== */
.qode-pricing-calculator{

	$pricing-table-border-color: #e6e6e6;

	display: table;
	table-layout: fixed;
	width: 100%;
	box-sizing: border-box;
	border: 1px solid;
	border-color: $pricing-table-border-color;

	.qode-pricing-calculator-items,
	.qode-pricing-calculator-text-holder {
		display: table-cell;
		vertical-align: middle;

	}
	.qode-pricing-calculator-items {
		width: 62%;
		box-sizing: border-box;
		border-right: 1px solid;
		border-color: inherit
	}
	.qode-pricing-calculator-text-holder {
		width: 38%;
		text-align: center;
		padding: 0 20px;
		box-sizing: border-box;
	}
	.qode-pricing-calculator-item {
		display: table;
		width: 100%;
		padding: 19px 15px;
		box-sizing: border-box;
		border-bottom: 1px solid;
		border-color: inherit;

		&:last-child {
			@media only screen and (min-width: $phone-landscape) {
				border-bottom: none;
			}
		}
	}

	.qode-pricing-calculator-switcher-holder,
	.qode-pricing-calculator-title-holder {
		display: table-cell;
		vertical-align: middle;
	}
	.qode-pricing-calculator-switcher-holder {
		width: 1%;
	}
	.qode-pricing-calculator-title-holder {
		padding-left: 22px;
	}

	.qode-pricing-calculator-total-price-holder {
		font-size: 45px;
		color: #303030;
	}
	.qode-pricing-calculator-text {
		margin-bottom: 0;
		padding: 0;
		margin-top: 32px;
	}
	.qode-pricing-calculator-subtitle {
		margin-top: 10px;
		margin-bottom: 0;
	}

	.qode-pricing-calculator-button-holder {
		display: block;
		margin-top: 52px;

		.qode-qbutton-full-width {
			padding-left: 0 !important;
			padding-right: 0 !important;
			box-sizing: initial !important;
		}
	}

	.qode-pricing-calculator-switch {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 77px;
		height: 40px;

		input {
			display:none;
		}

		.qode-pricing-calculator-slider {
			position: absolute;
			cursor: pointer;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: #eae8e8;
			box-shadow: inset 0 0 3px 1px rgba(0,0,0,.1);
			@include qodeTransition(.4s);
			border-radius: 34px;

			&:before {
				position: absolute;
				content: "";
				height: 34px;
				width: 34px;
				left: 3px;
				bottom: 3px;
				background-color: #fff;
				box-shadow: 0 0 3px 1px rgba(0,0,0,0.1);
				border-radius: 50%;
				@include qodeTransition(.4s);
			}
		}

		input:checked + .qode-pricing-calculator-slider {
			background-color: $first-main-color;
		}


		input:checked + .qode-pricing-calculator-slider:before {
			@include qodeTransform(translateX(37px));
		}

	}




}


/* ==========================================================================
   Pricing Calculator shortcode end styles
   ========================================================================== */