/* Pricing Table - begin
========================================================================== */
.q_price_table.qode_pricing_table_advanced{

	color: inherit;
	border-top: none;

	.price_table_inner {
		background-color: transparent;
		position: relative;
		display: table;
		width: 100%;
		table-layout: fixed;

		.price {
			font-size: 77px;
			line-height: 60px;
			padding: 0 3px;
			font-weight: 200;
			vertical-align: middle;
			display: inline-block;
		}
		.mark {
			position: relative;
			line-height: 19px;
			vertical-align: bottom;
			margin-top: 31px;
			display: block;
			font-size: 13px;
		}
		.value {
			position: relative;
			top: -13px;
			font-weight: 700;
			font-size: 19px;
			color: $first-main-color;
		}
	}
	.qode_pt_subtitle {
		display: block;
		font-size: 13px;
		color: $first-main-color;
	}

	.qode_price_table_prices {
		display: table-cell;
		vertical-align: top;
		width: 40%;
		text-align: right;
	}

	.qode_pricing_table_text {
		list-style: none;
		display: table-cell;
		vertical-align: top;
		width: 60%;
		padding-left: 30px !important;
		margin: 0;

		li {
			text-align: left;
			padding: 0;

			&.qode_pt_title {

				padding: 2px 0 18px;

			}

			ul {

				padding: 18px 0 25px;

				li {
					padding: 5px 0;
				}
			}
		}
	}

	.price_button .qbutton {
		margin: 0;
	}
	.qode_pt_additional_info {
		margin-top: 40px;
		padding-top: 20px;
		border-top: 1px solid #e3e3e3;
		font-size: 13px;

		.qode_pt_icon {
			color: $first-main-color;
			font-size: 19px;
			margin-right: 12px;
			vertical-align: middle;
		}
	}
	.qode_pt_image {
		margin-bottom: 38px;
	}
}


/* Pricing Table - end
========================================================================== */