/* ==========================================================================
   Start of Video Box Shortcode
   ========================================================================== */

@mixin transition($param) {
    transition: $param;
    -webkit-transition: $param;
}

@mixin transform($param){
    transform: $param;
    -webkit-transform: $param;
}

@mixin border-radius($param){
    -webkit-border-radius: $param;
    -moz-border-radius: $param;
    border-radius: $param;
}

@mixin box-sizing($param){
    -webkit-box-sizing: $param;
    -moz-box-sizing: $param;
    box-sizing: $param;
}


.qode_video_box {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    max-width: 100%;

    .qode_video_image {
        display: block;
        position: relative;
        overflow: hidden;

        &::after{
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: rgba(255, 255, 255, 0.1);
            z-index: 33;
            opacity: 0;
            @include transition(opacity .3s ease-out);
        }

        &:hover{
            &::after{
                opacity: 1;
            }

            img{
                @include transform(scale(1.05) rotate(0.01deg));
            }

            .qode_video_box_button {
                background-color: #1abc9c;
            }

            .qode_video_box_button_arrow{
                border-left-color: #fff;
            }
        }

        img{
            display: block;
            position: relative;
            max-width: 100%;
            z-index: 22;
            -webkit-transition: -webkit-transform 0.2s ease-out;
            transition: transform 0.2s ease-out;
            -webkit-backface-visibility: hidden;
        }

        .qode_video_box_button_holder {
            position: absolute;
            top: 50%;
            left: 50%;
            text-align: center;
            @include transform(translate(-50%, -50%));
            z-index: 44;
        }

        .qode_video_box_button {
            display: inline-block;
            vertical-align: middle;
            position: relative;
            width: 80px;
            height: 80px;
            background-color: #fff;
            overflow: hidden;
            @include border-radius(50%);
            @include box-sizing(border-box);
            @include transition(background-color 0.15s ease-out);
        }

        .qode_video_box_button_arrow {
            position: absolute;
            top: 50%;
            left: 50%;
            @include transform(translate(-48%,-50%));
            border-top: 12px solid transparent;
            border-left: 18px solid #303030;
            border-bottom: 12px solid transparent;
            @include box-sizing(border-box);
        }

    }

    &.disabled_hover_overlay{
        .qode_video_image::after {
            display: none;
        }
    }

    &.disabled_hover_zoom{
        .qode_video_image{
            &:hover{
                img{
                    @include qodeTransform(none);
                }
            }
        }
    }
}


/* ==========================================================================
   End of Video Box Shortcode
   ========================================================================== */