/* ==========================================================================
   #Specification List style - start
   ========================================================================== */
.qode-specification-list{

	background-color: #fff;

	.qode-specification-list-image {
		img {
			vertical-align: middle;
		}
	}

	.qode-specification-list-text-holder {
		padding: 28px 34px 34px;
	}

	.qode-specification-list-items {
		margin-top: 34px;
	}

	.qode-specification-list-item {
		display: table;
		width: 100%;
		table-layout: fixed;
		border-bottom: 1px solid #e6e6e6;
		padding: 4px 0;

		&:last-child {
			border-bottom: none;
		}

		.qode-specification-list-item-label,
		.qode-specification-list-item-value {
			width: 50%;
			display: table-cell;
			vertical-align: middle;
		}

		.qode-specification-list-item-value {
			text-align: right;
		}

	}


}
/* ==========================================================================
   #Specification List style - end
   ========================================================================== */