.qode-cards-gallery-holder{
    padding: 10px 0px;
    position: relative;

    .qode-cards-gallery{
        width: 100%;
    }

    img{
        position: relative;
        display: block;
        @include qodeTransition(opacity 0.3s ease);
    }

    .fake_card{
        position: relative;
        display: block;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;

    }

    .card {
        box-sizing: border-box;
        position: absolute;
        @include qodeTransition(transform 0.5s ease-in-out, margin 0.3s ease-in-out, left 0.3s ease-in-out, right 0.3s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out);
        width: 100%;
        background-color: #fff;
        @include qodeBoxShadow(0 0 6px 0 rgba(0, 0, 0, 0.2));

        &:not(:last-of-type) {
            cursor: pointer;
        }
    }

    &.left .card{
        &.out{
            @include qodeTransition(transform 0.5s ease-in-out, left 0.3s ease-in-out);
        }

        &:nth-last-child(8) {
            @include qodeTransform(scale(0.3));
            left: -70%;
        }

        &:nth-last-child(7) {
            @include qodeTransform(scale(0.4));
            left: -60%;
        }

        &:nth-last-child(6) {
            @include qodeTransform(scale(0.5));
            left: -50%;
        }

        &:nth-last-child(5) {
            @include qodeTransform(scale(0.6));
            left: -40%;
        }

        &:nth-last-child(4) {
            @include qodeTransform(scale(0.7));
            left: -30%;
        }

        &:nth-last-child(3) {
            @include qodeTransform(scale(0.8));
            left: -20%;
        }

        &:nth-last-child(2) {
            @include qodeTransform(scale(0.9));
            left: -10%;
        }

        &:nth-last-child(1) {
            padding: 0;
            left: 0px;
        }

        &:not(:last-of-type):not(.active):hover {
            margin-left: -10px !important;
        }
    }

    &.right .card{
        &.out{
            @include qodeTransition(transform 0.5s ease-in-out, right 0.3s ease-in-out);
