/* ==========================================================================
   Lazy Load styles - start
   ========================================================================== */
@-webkit-keyframes outer {
    0% {
        @include qodeTransform(rotate(0deg));
    }

    100% {
        @include qodeTransform(rotate(360deg));
    }
}

@keyframes outer {
    0% {
        @include qodeTransform(rotate(0deg));
    }

    100% {
        @include qodeTransform(rotate(360deg));
    }
}

@-webkit-keyframes inner {
    0% {
        @include qodeTransform(rotate(-100.8deg));
    }

    100% {
        @include qodeTransform(rotate(0deg));
    }
}

@keyframes inner {
    0% {
        @include qodeTransform(rotate(-100.8deg));
    }

    100% {
        @include qodeTransform(rotate(0deg));
    }
}

@-webkit-keyframes arc {
    0% {
        stroke-dasharray: 1 210.48671px;
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dasharray: 151.55043px, 210.48671px;
        stroke-dashoffset: 0;
    }
    100% {
        stroke-dasharray: 1 210.48671px;
        stroke-dashoffset: -151.55043px;
    }
}

@keyframes arc {
    0% {
        stroke-dasharray: 1 210.48671px;
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dasharray: 151.55043px, 210.48671px;
        stroke-dashoffset: 0;
    }
    100% {
        stroke-dasharray: 1 210.48671px;
        stroke-dashoffset: -151.55043px;
    }
}

.qode-lazy-preloader {
    font-size: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -37.5px 0 0 -37.5px;
    display: inline-block;
    line-height: normal;
    z-index: 10;
    -webkit-animation: outer 6600ms linear infinite;
    animation: outer 6600ms linear infinite;
}

.qode-lazy-preloader svg {
    -webkit-animation: inner 1320ms linear infinite;
    animation: inner 1320ms linear infinite;
}

.qode-lazy-preloader svg circle {
    fill: none;
    stroke: $first-main-color;
    stroke-linecap: round;
    -webkit-animation: arc 1320ms cubic-bezier(0.8, 0, 0.4, 0.8) infinite;
    animation: arc 1320ms cubic-bezier(0.8, 0, 0.4, 0.8) infinite;
}

img[data-lazy="true"] {
    opacity: 0;
}

/* ==========================================================================
   Lazy Load styles - end
   ========================================================================== */

/*gradient styles - start*/
.qode-type1-gradient-left-to-right {
    @include qodeType1LeftToRightGradient();
}

.qode-type1-gradient-left-to-right-after {
    &:after {
        @include qodeType1LeftToRightGradient();
    }
}

.qode-type1-gradient-bottom-to-top {
    @include qodeType1BotomToTopGradient('false');
}

.qode-type1-gradient-bottom-to-top-after {
    &:after {
        @include qodeType1BotomToTopGradient('false');
    }
}

.qode-type1-gradient-left-bottom-to-right-top {
    @include qodeType1LeftBottomToRightTopGradient('false');
}

.qode-type1-gradient-left-to-right-2x {
    @include qodeType1LeftToRightGradient2x();
    background-position: 0%;
    background-size: 200% 200%;
}

.qode-type1-gradient-left-to-right-text {
    i, i:before, span {
        @include qodeType1LeftBottomToRightTopGradient('true');
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: $type1-gradient-first-color;
    }
}

.qode-type1-gradient-bottom-to-top-text {
    i, i:before, span, span span {
        @include qodeType1BotomToTopGradient('true');
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: $type1-gradient-first-color;
    }
}

.qode-type1-gradient-bottom-to-top-text-hover {
    &:hover {
        i, i:before, span, span span {
            @include qodeType1BotomToTopGradient('true');
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            color: $type1-gradient-first-color;
        }
    }
}

/*gradient styles - start*/