/* ==========================================================================
   #Image with icon and text style - start
   ========================================================================== */
.qode-image-with-icon-and-text{

	text-align: center;

	.qode-iwiat-image {
		img {
			vertical-align: middle;
		}
	}

	.qode-iwiat-image-icon-holder {

		position: relative;
		margin-bottom: 63px;

		.qode-iwiat-icon-holder {
			position: absolute;
			text-align: center;
			left: 0;
			bottom: 0;
			@include qodeTransform(translateY(50%));
			width: 100%;
		}


	}

	.qode-iwiat-text {
		margin-top: 10px;
	}
}
/* ==========================================================================
   #Image with icon and text style - end
   ========================================================================== */