/* ==========================================================================
   #Advanced Pricing List style - start
   ========================================================================== */
.qode-advanced-pricing-list{

	box-sizing: border-box;

	.qode-apl-title {
		margin-bottom: 30px;
	}

	.qode-apl-item {

		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0;
		}

		.qode-apl-item-top {
			display: table;
			width: 100%;

			> * {
				display: table-cell;
				vertical-align: middle;
				box-sizing: border-box;
			}
			.qode-apl-line {
				height: 3px;
				border-bottom: 1px dotted #828383;
				display: table-cell;
				width: 98%;
				z-index: 1;
				position: relative;
				bottom: 6px;
			}
			.qode-apl-item-title {
				padding-right: 8px;
				white-space: nowrap;
			}
			.qode-apl-item-price {
				padding-left: 8px;
				white-space: nowrap;
			}
		}
	}

	.qode-apt-title-holder,
	.qode-apt-item-title {
		padding:21px 0 21px 30px;
	}

	.qode-apt-column-title-holder,
	.qode-apt-item-price {
		text-align: right;
		padding-right: 35px;

	}

	.qode-apt-footer {
		table-layout: auto;
		width: 100%;
	}

	.qode-apt-column-footer-image {
		padding: 21px 30px;

		img {
			vertical-align: middle;
		}
	}

	.qode-apt-column-footer-text {
		text-align: right;
		padding-right: 30px;
		font-size: 13px;
	}
}
/* ==========================================================================
   #Advanced Pricing List style - end
   ========================================================================== */