.qode-info-card{

	text-align: center;

	.qode-info-card-image {

		background-color: #fff;

		img {
			vertical-align: middle;
		}

	}

	.qode-info-card-text-holder {
		padding: 32px 40px 59px;
		background-color: #fff;
	}

	.qode-info-card-text {
		margin-top: 18px;
	}
}
