.qode-panel-area{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 6000;
    outline: 0!important;
    background-color: #000;
    transform: translateY(-100%);
    visibility: hidden;
    pointer-events: none;
    transition: all .3s cubic-bezier(1,.28,.15,.95);

    .qode-panel-area-opened &{
        visibility: visible;
        pointer-events: auto;
        transform: translateY(0);
        transition: all .4s cubic-bezier(1,.28,.15,.95);
    }

    .qode-panel-area-inner{
        @include qodeTableLayout();

        .qode-panel-area-content{
            @include qodeTableCellLayout();
        }
    }

    .qode-close-panel-holder{
        position: absolute;
        right: 120px;
        top: 75px;
        z-index: 9999;
    }
}