/* ==========================================================================
   Item Showcase start styles
   ========================================================================== */
.qode-item-showcase {
	text-align: center;
	opacity: 0;

	.touchevents & {
		opacity: 1;
	}

	.qode-item-image {
		display: inline-block;
	 	margin-top: -70px;
	 	opacity:0;
		width:36%;
	 	@include qodeTransform(translateY(70px));

	 	img {
	 		vertical-align: middle;
	 	}

	 	.touchevents &,
	 	&.qode-appeared {
	 	    -webkit-transition: opacity .55s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform .55s cubic-bezier(0.23, 1, 0.32, 1);
	 	    transition: opacity .55s cubic-bezier(0.23, 1, 0.32, 1), transform .55s cubic-bezier(0.23, 1, 0.32, 1);
	 	    opacity: 1;
	 	    @include qodeTransform(translateY(0));
	 	}
	}

	.qode-item {
		display: inline-block;
		opacity: 0;

		&.qode-item-left {
			@include qodeTransform(translateX(-70px));
			text-align: right;
			.qode-item-icon{
				padding-left:30px;
			}
		}

		&.qode-item-right {
			text-align: left;
			@include qodeTransform(translateX(70px));
			.qode-item-icon{
				padding-right:30px;
			}
		}

	 	.touchevents &,
		&.qode-appeared {
		    -webkit-transition: opacity .55s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform .55s cubic-bezier(0.23, 1, 0.32, 1);
		    transition: opacity .55s cubic-bezier(0.23, 1, 0.32, 1), transform .55s cubic-bezier(0.23, 1, 0.32, 1);
		    opacity: 1;
		    @include qodeTransform(translateX(0));
		}

		.qode-item-icon{
			display:table-cell;
			font-size:30px;

			i{
			    padding: 15px;
				font-size: 17px;
				color: #fff;
				background-color: #2a2a2a;
				@include qodeTransition(opacity .15s ease-out);
			}

			i:hover {
				opacity: .94;
			}
		}

		.qode-item-content {
			display: table-cell;
			vertical-align: top;

			.qode-showcase-title-holder {
				.qode-showcase-title {
					position: relative;
					display: inline-block;
					margin:0;
					font-size: 25px;
                    letter-spacing: 0px;
                    @include qodeTransition(color .15s ease-in-out);
				}

				a:hover {
					.qode-showcase-title {
						color: inherit;
					}
				}
			}

			.qode-showcase-text-holder {
				.qode-showcase-text {
                    margin: 7px 0 44px;
				}
			}
		}
	}

	.qode-item-showcase-holder {
		position: relative;
		display: inline-block;
		width:32%;

		.qode-item-line {
		    content: '';
		    position: absolute;
		    top: 0;
		    width: 0;
		    height: 0;
		    border-left: 1px dashed #2a2a2a;
		    opacity: 0;
		    @include qodeTransition(all .55s cubic-bezier(0.23, 1, 0.32, 1) .25s);
		    z-index: -1;

		    .touchevents & {
		    	@include qodeTransition(none);
		    }
		}

		&.qode-holder-left {
			float: left;
			text-align: right;
			.qode-item-line {
			    right: 23px;
			}
		}

		&.qode-holder-right {
			float: right;
			text-align: left;
			.qode-item-line {
				left:23px;
			}
		}
	}
}
/* ==========================================================================
   Item Showcase end styles
  ========================================================================== */
