.blog_holder.blog_chequered {
    overflow: visible !important;
    opacity: 0;
	margin-bottom: 30px;

    .blog_holder_grid_sizer{
        width: 25%;
    }

    .blog_holder_grid_gutter{
        width: 0%;
    }

    #infscr-loading{
        bottom: -35px;
    }

    article {

        display: block;
        margin-bottom: 0;
        width: 25%;
        padding: 0;
        text-align: left;
        vertical-align: top;
        z-index: 100;
        box-sizing: border-box;

        &.large-width-height,
        &.large-width {
            width: 50%;
        }

        .ql_full_link{
            display: block;
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 100;
        }

        .post_image {
            > a {
                height: 100%;
                display: block;
                position: relative;
            }

            > a:after,
            .flexslider .slides a:after {
                background: rgba(0, 0, 0, 0.13) none repeat scroll 0 0;
                content: "";
                height: 100%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 0;
                width: 100%;
                z-index: 3;
                @include qodeTransition(opacity .2s cubic-bezier(0.3, 0.67, 0.24, 0.86));
            }
        }

        .post_text {
            display: block;
            position: absolute;
            top: 0;
	        height: 100%;
            width: 100%;
            padding: 45px 50px 0;
            text-align: left;
            z-index: 99;
            box-sizing: border-box;

            h5 {
	            font-size: 23px;
	            line-height: 29px;
	            text-transform: none;
	            font-weight: 700;
	            margin-bottom: 0;

	            a {
		            color: #fff;
	            }
            }

            .post_text_inner {
                padding: 0;
                background-color: transparent;
            }
        }
	    span.audio_icon,
	    span.video_icon {
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    transform: translate(-50%, -50%);
		    z-index: 10;
		    background-color: #fff;
		    border-radius: 100%;
		    width: 62px;
		    height: 62px;
		    line-height: 62px;
		    text-align: center;
		    color: #393939;
		    font-size: 20px;
	    }
	    .date {
		    display: block;
		    font-size: 10px;
		    text-transform: uppercase;
		    font-weight: 700;
		    color:#fff;
		    margin-bottom: 4px;
	    }

	    .post_info {
            margin-bottom: 0;
            margin-top: 0;
            color:#fff;
			position: absolute;
			bottom: 40px;
	        left: 50px;
			font-size: 10px;
	        text-transform: uppercase;
			letter-spacing: 2px;
		    font-weight: 700;

            a {
                color: #fff;
            }


            .social_share_list_holder ul li i{
                color: #fff;
            }
        }
        .post_content_holder,
        .post_image{
            height: 100%;
        }
	    .post_content_holder {
		    overflow: hidden;
		    position: relative;
        }


        &.format-quote,
        &.format-link {
            background-color: #fff;
            -webkit-transition: background-color .3s ease;
            transition: background-color .2s ease;


            .post_text {
                top: 50%;
                bottom: auto;
                @include qodeTransform(translateY(-50%));
                text-align: left;
                padding: 0px 45px;
	            height: auto;

                &,&:hover {
                    .post_text_inner {
                        padding: 0;
                        background-color: transparent !important;
                    }
                }
            }

            .post_title{
                padding: 0px;

                p{
                    font-size: 17px;
	                line-height: 23px;
                    font-weight: 700;
                }
            }

            .quote_author{
                font-size: 10px;
                font-weight: 700;
	            text-transform: uppercase;
	            letter-spacing: 2px;
            }

            .post_title a,
            .post_title span,
            i.qoute_mark,
            i.link_mark,
            .post_info span,
            .social_share_list_holder ul li i{
                color: #212121;
                @include qodeTransition(color .2s cubic-bezier(0.3, 0.67, 0.24, 0.86));
            }

            i.link_mark{
                position: absolute;
                top: -14px;
                right: 11px;
                font-size: 77px;
	            color: #e1e1e1;
            }

	        i.qoute_mark {
		        position: absolute;
		        top: -50px;
		        right: -18px;
		        font-size: 125px;
		        color: #e1e1e1;
	        }

            &:hover{
                background-color: $first-main-color;

                .post_title a,
                .post_title span,
                i.qoute_mark,
                i.link_mark,
                .post_info span,
                .social_share_list_holder ul li i{
                    color: #fff;
                }
            }
        }

        &:not(.format-link):not(.format-quote) {
			.post_text {
			    opacity: 0;
			    background-color: rgba(0, 0, 0, 0.6);
				@include qodeTransition(opacity 0.2s ease-out);
			}
           &:hover {
                @include qodeTransition(background-color .2s cubic-bezier(0.3, 0.67, 0.24, 0.86));
	           .post_text {
		           opacity: 1;
	           }

                .post_image {
                    > a:after,
                    .flexslider .slides a:after{
                        opacity: 1;
                    }
                }
            }
        }
    }
}