.qode-btn {
    display: inline-block;
    position: relative;
    outline: none;
    font-style: normal;
    font-weight: 500;
    font-family: inherit;
    padding: 18px 25px;
    font-size: 14px;
    line-height: 1.7em;

    @include qodeTransition(color 0.15s ease-out, background-color 0.15s ease-out, border-color 0.15s ease-out);
    @include qodeBoxSizing(border-box);

	&.qode-btn-simple {
		padding: 0 !important;
		color: #303030;
		background-color: transparent;
		border: 0;
		vertical-align: middle;

		.qode-btn-text {
			display: inline-block;
			vertical-align: middle;
		}

		&:not(.qode-btn-custom-hover-color):hover {
			//important because of inline color attribute. :not is used so we don't have to use important in JS
		}

		&:not(.qode-btn-custom-hover-bg):hover {
			//important because of inline color attribute. :not is used so we don't have to use important in JS
			background-color: transparent !important;
		}

		&:not(.qode-btn-custom-border-hover):hover {
			//important because of inline color attribute. :not is used so we don't have to use important in JS
			border-color: transparent !important;
		}
	}

    &.qode-btn-solid {
        background-color: #fff;
        border: none;
        color: #303030;

        &:not(.qode-btn-custom-hover-color):hover {
            //important because of inline color attribute. :not is used so we don't have to use important in JS
        }

        &:not(.qode-btn-custom-hover-bg):hover {
            //important because of inline color attribute. :not is used so we don't have to use important in JS

        }

        &:not(.qode-btn-custom-border-hover):hover {
            //important because of inline color attribute. :not is used so we don't have to use important in JS

        }
    }

    &.qode-btn-outline {
        border: 2px solid #000;
        color: #000;
        background-color: transparent;

        &:not(.qode-btn-custom-hover-color):hover {
            //important because of inline color attribute. :not is used so we don't have to use important in JS
            color: #fff !important;
        }

        &:not(.qode-btn-custom-hover-bg):hover {
            //important because of inline color attribute. :not is used so we don't have to use important in JS
            background-color: #000 !important;
        }

        &:not(.qode-btn-custom-border-hover):hover {
            //important because of inline color attribute. :not is used so we don't have to use important in JS
            border-color: #000 !important;
        }
    }

    &.qode-btn-small {
        font-size: 10px;
        padding: 5px 15px;
    }

    &.qode-btn-medium {
        //same as default
    }

    &.qode-btn-large {
        font-size: 14px;
        padding: 12px 28px;
    }

    &.qode-btn-huge,
    &.qode-btn-huge-full-width {
        font-size: 16px;
        padding: 15px 31px;
    }

    &.qode-btn-huge-full-width {
        display: block;
        text-align: center;
    }

	&.qode-btn-with-shadow {
		@include qodeBoxShadow(0 4px 16px #e2e2e2);
	}

	&.qode-btn-icon {
		padding: 0;
		height: 64px;
		overflow: hidden;
        vertical-align: middle;

		&.qode-btn-simple {

			height: auto;

			.qode-btn-text {
				padding-right: 0;
				padding-left: 0;
			}
			.qode-button-v2-icon-holder {
				padding: 0 0 0 8px;
				font-size: inherit;
			}
		}

		&.qode-btn-solid {
			line-height: 64px;

			&.qode-btn-icon-square .qode-button-v2-icon-holder{
				width: 64px;
				box-sizing: border-box;
			}
		}
		&.qode-btn-outline {
			line-height: 60px;


			&.qode-btn-icon-square .qode-button-v2-icon-holder{
				width: 60px;
				box-sizing: border-box;
			}
		}

		.qode-btn-text {
			padding-right: 35px;
			padding-left: 35px;
			display: inline-block;
			vertical-align: top;
		}

		.qode-button-v2-icon-holder {
			font-size: 28px;
			line-height: inherit;
			text-align: center;
			display: inline-block;
			vertical-align: top;
			padding: 0 14px;
			border-left: 1px solid #f3f3f3;
			-moz-transform:translateZ(0); //fix jumpy pixel in firefox
			@include qodeTransition(border-color 0.15s ease-out, background-color 0.15s ease-out);

			.qode-button-v2-icon-holder-inner {
				line-height: inherit;
			}
		}

		&:hover .qode-button-v2-icon-holder:before {
			@include qodeAnimation(qode-point 2s infinite ease-out);
		}

		&.qode-btn-icon-rotate {
			.qode-button-v2-icon-holder {
				position: relative;
				padding: 0;
				overflow: hidden;

				.qode-button-v2-icon-holder-inner {
					position: relative;
					left: 0;
					display: inline-block;
					padding: 0 14px;
					width: inherit;
					box-sizing: border-box;
					@include qodeTransition(left .2s ease-out);
				}
			}

			.qode-button-v2-icon-holder:before{
				display: inline-block;
				width: 100%;
				height: 100%;
				position: absolute;
				left: -100%;
				top: 0;
				content: "\24";
				font-family: 'ElegantIcons';
				overflow: hidden;
				font-weight: 400;
				@include qodeTransition(left .2s ease-out);
			}

			&:hover .qode-button-v2-icon-holder:before{
				left: 0;
			}

			&:hover .qode-button-v2-icon-holder .qode-button-v2-icon-holder-inner{
				left: 100%;
			}
		}

	}
	
}

input[type=submit].qode-btn,
button.qode-btn {
    cursor: pointer;
}


//3d rotate hover
.qode-3d-button-holder {
    display: inline-block;
    position: relative;
    -webkit-perspective: 1000px;
    -moz-perspective: 1000px;
    -o-perspective: 1000px;
    -ms-perspective: 1000px;
    perspective: 1000px;
    opacity: 0; //show in JS when sides are set

    .qode-btn.qode-btn-3d-hover {
        @include qodeTransition(all .2s ease-out);
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
        border-radius: 2px;
        visibility: visible;

        &:last-child {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            visibility: hidden;
        }
    }

    &:hover {
        .qode-btn.qode-btn-3d-hover {
            &:first-child {
                visibility: hidden;
            }

            &:last-child {
                visibility: visible;
            }
        }
    }
}

//shadow enahance hover
.qode-btn-shadow-hover {
    @include qodeTransition(all .2s ease-out);

    &:hover {
        @include qodeBoxShadow(4px 8px 16px #dedede);
    }
}
