/* ==========================================================================
  	Advanced Call To Action shortcode start styles
   	========================================================================== */
.qode-advanced-call-to-action {
	position: relative;
	display: table;
	width: 100%;
	height: 110px;
	text-align: center;

	.advanced-cta-link {
		@include qodeAbsoluteHolderLayout();
		z-index: 100;
	}

	.qode-advanced-cta-content {
		display: table-cell;
		vertical-align: middle;
		position: relative;
		z-index: 20;

		.qode-advanced-cta-text-holder {
			display: inline-block;
			vertical-align: middle;
		}
	}

	.qode-advanced-cta-text-holder {
		color: #fff;
		font-size: 15px;
		font-weight: 500;
		line-height: 1;
	}

	.qode-advanced-cta-icon-holder {
		position: relative; 
		display: inline-block;
		vertical-align: middle;
		margin-left: 17px;
		color: #fff;
		font-size: 27px;

		.qode-advanced-cta-icon-holder-table {
			display: table;
			height: 100%;
			width: 100%;
		}

		.qode-advanced-cta-icon-holder-cell {
			position: relative;
			display: table-cell;
			vertical-align: middle;
		}
	}

	&.qode-advanced-cta-icon-circle {
		.qode-advanced-cta-icon-holder {
			height: 46px;
			width: 46px;
		}

		.qode-advanced-cta-icon-border {
		    position:absolute;
		    top:0%;
		    left:0%;
		    height: calc(100% - 4px);
		    width: calc(100% - 4px);
		    border:2px solid currentColor;
		    border-radius:50%;
		    -webkit-transition: -webkit-transform .25s cubic-bezier(0.11, 0.76, 0.83, 1.62);
		    transition: transform .25s cubic-bezier(0.11, 0.76, 0.83, 1.62);
		}

		&:hover {
			.qode-advanced-cta-icon-border {
		    	@include qodeTransform(scale(1.12));
			}
		}
	}

	&.qode-advanced-cta-gradient {
		.qode-advanced-cta-background-holder {
			@include qodeAbsoluteHolderLayout();
			overflow: hidden;
		}

		.qode-advanced-cta-background-1,
		.qode-advanced-cta-background-2 {
			@include qodeAbsoluteHolderLayout();
			z-index: -1;
		}
	}

	&.qode-advanced-cta-gradient-animation {
		@keyframes qodeFlowAnimation2 {
			0%,100% {
		   		opacity: 0;
			}  

			50% {
				opacity: 1;
			}  
		}

		.qode-advanced-cta-background-2 {
			@include qodeTransform(scaleX(-1));
			@include qodeAnimation(qodeFlowAnimation2 4s ease infinite);
		}

		@keyframes qodeFlowAnimation3 {
			0%,100% {
		   		opacity: 0;
			}  

			50% {
				opacity: .35;
			}  
		}

		.qode-advanced-cta-background-3 {
			opacity:0;
			@include qodeAbsoluteHolderLayout();
			@include qodeAnimation(qodeFlowAnimation3 2s ease infinite 2s);
			background-color: $first-main-color;
		}
	}

	&:not(.qode-advanced-cta-gradient-animation) {
		.advanced-cta-link {
			&:after {
				content:'';
				@include qodeAbsoluteHolderLayout();
				z-index: -1;
				background-color: rgba(255,255,255,.03);
				opacity: 0;
				@include qodeTransition(opacity .2s);
			}
		}
		&:hover {
			.advanced-cta-link {
				&:after {
					opacity: 1;
				}
			}
		}
	}
}
/* ==========================================================================
  	Advanced Call To Action shortcode end styles
  	========================================================================== */
