/* ==========================================================================
  	Scrolling Image shortcode start styles
   	========================================================================== */
.qode-scrolling-image-holder {
	$scrolling-icon-size: 96px;
	text-align: center;
	visibility: hidden;

	.qode-si-title-holder {
		margin-bottom: 65px;
	}	

	.qode-si-content-holder {
		position: relative;
		margin-bottom: $scrolling-icon-size/2;

		a {
			position: absolute;
			top:0;
			left:0;
			height: 100%;
			width: 100%;
			z-index: 100;
		}

		&:hover {
			.qode-si-icon-holder .qode-si-icon-background{ 
				@include qodeTransition(all .3s cubic-bezier(0.42, 0, 0, 2.38));
				@include qodeTransform(scale(1.06));
			}
		}
	}

	.qode-si-image-holder {
		position: relative;
		overflow: hidden;

		.qode-si-image-holder-inner {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
		}

		.qode-si-image {
			width: 100%;
			min-height: 100%;
			-webkit-transition-timing-function: cubic-bezier(0.49, 0.13, 0.16, 0.96);
			transition-timing-function: cubic-bezier(0.49, 0.13, 0.16, 0.96);
			will-change: transform;
			-webkit-filter: blur();
			filter: blur();
		}

		.qode-si-frame {
			vertical-align: middle;
		}
	}

	.qode-si-icon-holder {
		position: absolute;
		height: $scrolling-icon-size;
		width: $scrolling-icon-size;
		left: 50%;
		bottom: -$scrolling-icon-size/2;
		@include qodeTransform(translateX(-50%));
		border-radius: 50%;

		.qode-si-icon-table {
			position: relative;
			display: table;
			height: 100%;
			width: 100%;
			z-index: 10;

			.qode-si-icon-cell {
				display: table-cell;
				vertical-align: middle;
			}
		}

		.qode-si-icon {
			font-size: 45px;
			color: #fff;
		}

		.qode-si-icon-background {
			position: absolute;
			top:0;
			left:0;
			height: 100%;
			width: 100%;
			background-color: $first-main-color;
			border-radius: 50%;
			@include qodeTransition(all .2s);
			will-change: transform;
		}
	}

	&.qode-si-box-shadow-yes {
		.qode-si-image-holder,
		.qode-si-icon-holder {
			@include qodeTransition(box-shadow .2s);
			box-shadow: 0px 9px 38px 0px rgba(0, 0, 0, 0.13);
		}

		.qode-si-content-holder:hover {
			.qode-si-image-holder,
			.qode-si-icon-holder {
				box-shadow: 0px 12px 48px 0px rgba(0, 0, 0, 0.17);
			}
		}
	}

	&.qode-si-rounded-yes {
		.qode-si-image-holder {
			border-radius: 7px;
		}
	}
}
/* ==========================================================================
  	Scrolling Image shortcode end styles
  	========================================================================== */
