.blog_holder.blog_gallery {
    overflow: visible !important;
    opacity: 0;

    .blog_holder_grid_sizer{
        width: 33.33%;
    }

    .blog_holder_grid_gutter{
        width: 0%;
    }

    #infscr-loading{
        bottom: -35px;
    }

    article {

        display: block;
        margin-bottom: 0;
        width: 33.33%;
        padding: 0;
        text-align: left;
        vertical-align: top;
        z-index: 100;
        box-sizing: border-box;

        &.large-width-height,
        &.large-width {
            width: 50%;
        }

        .post_image {
            > a {
                height: 100%;
                display: block;
                position: relative;
            }

            > a:after,
            .flexslider .slides a:after {
                background: rgba(0, 0, 0, 0.13) none repeat scroll 0 0;
                content: "";
                height: 100%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 0;
                transition: opacity 0.2s ease-out 0s;
                width: 100%;
                z-index: 3;
            }
        }
	    .post_overlay {
		    position: absolute;
		    z-index: 98;
		    top: 20%;
		    left: 0;
		    right: 0;
		    bottom: 0;
		    background: -webkit-linear-gradient(transparent,rgba(0,0,0,1));
		    background: linear-gradient(transparent,rgba(0,0,0,1));
	    }
		.post_text {
            display: block;
            position: absolute;
            bottom: 50px;
            width: 100%;
            padding: 0 65px;
            text-align: left;
            z-index: 99;
            box-sizing: border-box;

            h5 {
	            margin: 0 0 18px;
                @include qodeTransition(all .2s);
		        a {
	                color: #fff;
		            text-transform: none;
		            font-size: 36px;
		            line-height: 42px;
		            font-weight: 700;
	            }
            }
            .post_excerpt {
                visibility: hidden;//animate in JS
                opacity:0;
                color:#fff;
                @include qodeTransition(opacity .2s cubic-bezier(0.3, 0.67, 0.24, 0.86), visibility .2s);
                margin-bottom: 20px;
            }
            .post_text_inner {
                padding: 0;
                background-color: transparent !important;
            }
        }

        &:hover {
            .post_excerpt {
                @include qodeTransition(opacity .4s cubic-bezier(0.3, 0.67, 0.24, 0.86) .15s);
            }
        }

        .post_info {
            margin-bottom: 0;
            margin-top: 0;
            color:#fff;
	        text-transform: uppercase;
	        font-size: 10px;
	        letter-spacing: 3px;
            a:not(:hover) {
                color: #fff;
            }
        }
	    .post_category {
		    color:#fff;
		    text-transform: uppercase;
		    font-size: 10px;
		    letter-spacing: 3px;
		    display: block;
		    margin-bottom: 23px;
            @include qodeTransition(all .2s);

		    a {
			    color: #fff;
			    border: 1px solid #fff;
			    border-radius: 15px;
			    padding: 3px 12px;
			    @include qodeTransition(color 0.2s ease-out, border-color 0.2s ease-out);
		    }
	    }
        .post_content_holder,
        .post_image {
            height: 100%;
        }

        &:not(.format-link):not(.format-quote) {
            .post_text {
                -webkit-transition: -webkit-transform .3s cubic-bezier(0.44, 1.22, 0, 1.43);
                transition: transform .2s cubic-bezier(0.44, 1.22, 0, 1.43);
            }

        }
	    &.format-link,
	    &.format-quote {
		    .post_text:hover {

			    .post_info {

			    }
			    .post_text_inner {
				    background-color: transparent !important;
				    border-color: transparent !important;
			    }
		    }
	    }
    }
}