/* ==========================================================================
   #Advanced Pricing Table style - start
   ========================================================================== */
.qode-advanced-pricing-table{

	$apt-border-color: #e3e3e3;

	border-collapse: collapse;
	box-sizing: border-box;
	border: 1px solid $apt-border-color;


	.qode-apt-row {

		display: table;
		width: 100%;
		border-bottom: 1px dashed $apt-border-color;
		border-collapse: initial;

		> * {
			display: table-cell;
			vertical-align: middle;
			box-sizing: border-box;
		}

		&:last-child {
			border: none;
		}

		&:nth-child(odd) {
			background-color: #fff;
		}
		&:nth-child(even) {
			background-color: #f6f6f6;
		}
	}

	.qode-apt-title-holder,
	.qode-apt-item-title {
		padding:21px 0 21px 30px;
	}

	.qode-apt-column-title-holder,
	.qode-apt-item-price {
		text-align: right;
		padding-right: 35px;

	}

	.qode-apt-footer {
		table-layout: auto;
		width: 100%;
	}

	.qode-apt-column-footer-image {
		padding: 21px 30px;

		img {
			vertical-align: middle;
		}
	}

	.qode-apt-column-footer-text {
		text-align: right;
		padding-right: 30px;
		font-size: 13px;
	}
}
/* ==========================================================================
   #Advanced Pricing Table style - end
   ========================================================================== */