/* ==========================================================================
# Tabs style - begin
========================================================================== */
.qode-advanced-tabs {
    
    .qode-advanced-tabs-nav {                
        display: table;
        position: relative;
        list-style: none outside none;
        margin: 0;
        padding: 0;
        overflow: hidden;
        vertical-align: middle;
        width: 100%;
        @include qodeBoxSizing(border-box);

        li {
            margin: 0;
        	background-color: $first-main-color;

            a {
                position: relative;
                display: table;
                width: 100%;
                padding: 20px 28px;
                color:#fff;
                @include qodeBoxSizing(border-box);
                @include qodeTransition(color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out);

                i,
                span {
                    @include qodeTransition(initial);
                }

                .qode-advanced-icon-frame {
                    display: table-cell;
                    width: 20px;
                    font-size: 31px;
                    line-height: 1;
                    vertical-align: middle;
	                padding-right: 11px;
                }

                .qode-advanced-tab-text-after-icon {
                	display: table-cell;
                    @include qodeTransition(initial);
                    vertical-align: middle;
                }
            }
        }
    }

    .qode-advanced-tab-container {     
        position: relative;
	    padding: 35px 28px;
	    background-color: #fff;
        @include qodeBoxSizing(border-box);
    }
}

/* Icon tabs styles
   ========================================================================== */

.qode-advanced-tabs.qode-advanced-horizontal-tab {

	.qode-advanced-tabs-nav li {
		position: relative;
    	display: table-cell;
    	vertical-align: middle;

			a:after{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				background-color: rgba(255,255,255,0.05);
				@include qodeTransition(opacity .2s);
			}

			a span{
				position: relative;
				z-index: 1;
			}


		&.ui-state-active{
			background-color: #fff;

			a{
				color: #303030;
			}
		}

		&:not(.ui-state-active).ui-state-hover a:after{
			opacity: 1;
		}
	}
}

@for $i from 1 to 10 {
	.qode-advanced-tabs.qode-advanced-horizontal-tab.qode-advanced-tabs-column-#{$i} {

			.qode-advanced-tabs-nav li {
				width: 100% / $i;
			}

	}
}

/* ==========================================================================
# Tabs style - end
========================================================================== */