.qode-giwt{
    text-align: center;

    .qode-giwt-inner{
        display: inline-block;
        padding: 45px 20px;
        @include qodeTransition(background-color 0.2s ease, box-shadow 0.2s ease);

        &:hover{
            background-color: #fff;
            box-shadow: 0 7px 16px rgba(0,0,0,0.1);
        }

        .qode_icon_element{
            color: #b6b6b6;
        }

        .qode-giwt-content-holder{
            margin-top: 30px;

            a:hover{
                color: inherit;
            }
        }
    }
}