.blog_holder.masonry_gallery {
    overflow: visible !important;
    opacity: 0;

    .blog_holder_grid_sizer{
        width: 25%;
    }

    .blog_holder_grid_gutter{
        width: 0%;
    }

    #infscr-loading{
        bottom: -35px;
    }

    article {

        display: block;
        margin-bottom: 0;
        width: 25%;
        padding: 0;
        text-align: left;
        vertical-align: top;
        z-index: 100;
        box-sizing: border-box;

        &.large-width-height,
        &.large-width {
            width: 50%;
        }

        .ql_full_link{
            display: block;
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 100;
        }

        .post_image {
            > a {
                height: 100%;
                display: block;
                position: relative;
            }

            > a:after,
            .flexslider .slides a:after {
                background: rgba(0, 0, 0, 0.13) none repeat scroll 0 0;
                content: "";
                height: 100%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 0;
                -webkit-transition: opacity 0.2s ease-out 0s;
                transition: opacity 0.2s ease-out 0s;
                width: 100%;
                z-index: 3;
            }
        }

        .time {
            background-color: #fff;
            left: 0;
            margin: 0px;
            padding: 5px 13px;
            position: absolute;
            text-align: center;
            top: 0;
            color: #212121;
            line-height: 22px;
            font-size: 11px;
            z-index: 4;

            .time_day {
                font-size: 26px;
                line-height: 26px;
                font-weight: 700;
            }
            .time_year {
                color:#a7a7a7;
            }

            span {
                display: block;

            }
        }

        .post_text {
            display: block;
            position: absolute;
            bottom: 20px;
            width: 100%;
            padding: 0 60px;
            text-align: center;
            z-index: 99;
            box-sizing: border-box;

            h5 a{
                color: #fff;
                font-size: 28px;
                text-transform: none;
                font-weight: 700;
            }

            .post_text_inner {
                padding: 0;
                background-color: transparent;
            }
        }

        .post_info {
            margin-bottom: 10px;
            margin-top: 1px;
            color:#fff;

            a:not(:hover) {
                color: #fff;
            }


            .social_share_list_holder ul li i{
                color: #fff;
            }
        }
        .post_content_holder,
        .post_image{
            height: 100%;
        }


        &.format-quote,
        &.format-link {
            background-color: #fff;
            -webkit-transition: background-color .2s ease-out;
            transition: background-color .2s ease-out;

            .time {
                background-color: #212121;
                color: #fff;
                -webkit-transition: background-color .2s ease-out, color .2s ease-out;
                transition: background-color .2s ease-out, color .2s ease-out;

                .time_year {
                    color: #fff;
                }
            }

            .post_text {
                top: 50%;
                bottom: auto;
                @include qodeTransform(translateY(-50%));
                text-align: left;
                padding: 0px 57px;

                &,&:hover {
                    .post_text_inner {
                        padding: 0;
                        background-color: transparent !important;
                    }
                }
            }

            .post_title{
                padding: 0px;

                a{
                    font-size: 28px;
                    font-weight: 700;
                }
            }

            .quote_author{
                font-size: 12px;
                font-weight: 300;
            }

            .post_title a,
            .post_title span,
            i.qoute_mark,
            i.link_mark,
            .post_info span,
            .social_share_list_holder ul li i{
                color: #212121;
                -webkit-transition: color .2s ease;
                transition: color .2s ease;
            }

            i.qoute_mark,
            i.link_mark{
                position: absolute;
                top: 25px;
                right: 25px;
                font-size: 28px;
            }



            &:hover{
                background-color: $first-main-color;
                .entry_title {
                    text-decoration: underline;
                }
                .post_title a,
                .post_title span,
                i.qoute_mark,
                i.link_mark,
                .post_info span,
                .social_share_list_holder ul li i{
                    color: #fff;
                }
            }
        }

        &:not(.format-link):not(.format-quote) {
            &:hover {
                .entry_title {
                    text-decoration: underline;
                }
                .post_image {
                    > a:after,
                    .flexslider .slides a:after{
                        opacity: 1;
                    }
                }
            }
        }
    }
}