.blog_compound {

    article{
        border-bottom: 1px solid #e5e5e5;
        margin-bottom: 56px;
        padding-bottom: 58px;

        .qodef-image-shader {
            display: block;
            position: relative;
            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                opacity: 0;
                background-color: rgba(0,0,0,.3);
                @include qodeTransition(opacity .2s);
            }
        }

        .qode_blog_gallery_item.qode_blog_img_large_height {
            .qodef-image-shader {
                height: 100%;
            }
        }

        a:hover {
            .qodef-image-shader:after {
                opacity: 1;
                @include qodeTransition(opacity .4s);
            }
        }

        &:last-child{
            border: 0px;
        }

        .post_image img{
            position: relative;
            display: block;
        }

        .post_text {
            margin-top: 45px;
        }

        .post_meta{
            display: inline-block;
            width: 100%;
            vertical-align: middle;
            margin-top: 40px;

            .three_columns > .column1{
                text-align: left;
            }
            .three_columns > .column2{
                text-align: center;
            }
            .three_columns > .column3{
                text-align: right;
            }

            .post_comments,
            .blog_like a,
            .blog_share a{
                font-style: italic;

                &:not(:hover){
                    color: #7f7f7f;
                }
            }

            .blog_share{
                margin-left: 10px;
            }
        }

        .two_columns_50_50{
            .post_text{
                margin-top: 0px;
            }
        }

        .post_title{
            text-align: center;

            .category {
                margin-bottom: 10px;
                display: block;

                a, .date {
                    color: $first-main-color;
                    font-size: 10px;
                    letter-spacing: 2px;
                    text-transform: uppercase;
                    text-decoration: underline;
                }
            }
        }
    }

    &.sticky_posts{

        article:last-child{
            margin: 0px;
        }
        .post_title{
            text-align: center;
            margin: 45px 0px -15px;
        }
    }
}

.qode_blog_masonry_gallery {
    .qode_blog_gallery_sizer {
        width: calc((100% - 8px) / 2);
    }

    .qode_blog_gallery_gutter {
        width: 8px;
    }
    .qode_blog_gallery_item {
        box-sizing: border-box;
        width: calc((100% - 8px) / 2);
        a {
            display: block;
            padding: 0 0 8px;
            height: 100%;
            img {
                vertical-align: middle;
                height: calc(100% - 8px);
            }
        }

        &.qode_blog_img_large_height,
        &.qode_blog_img_large_height_width {
            box-sizing: border-box;
            a {
                height: calc(100% - 8px);
                img {
                    height: 100%;
                }
            }
        }
        &.qode_blog_img_large_height_width,
        &.qode_blog_img_large_width{
            width: 100%;
        }
    }
}