.qode-irc-holder{
	background-color: #fff;
	@include qodeBorderRadius(5px);

	.qode-irc-holder-inner{
		padding: 42px 67px 57px;
	}

	.qode-irc-title-holder{
		margin-bottom: 30px;
	}

	.qode-irc-sliders-holder{
		.qode-irc-range-slider-period-holder,
		.qode-irc-range-slider-loan-holder{
			margin-bottom: 37px;
			text-align: center;
			.qode-irc-range-slider-loan-holder-inner,
			.qode-irc-range-slider-period-holder-inner{
				font-size: 18px;
				font-weight: 500;
				color: #3e333f;
				margin-bottom: 10px;
			}

			.qode-irc-slider-period-value,
			.qode-irc-slider-loan-value{
				display: inline-block;

				&.irc-min{
					float: left;
				}

				&.irc-current{
					color: $first-main-color;
				}

				&.irc-max{
					float: right;
				}
			}
		}

		.irc-range-slider{
			width: 100%;
		}

		.rangeslider--horizontal{
				height: 6px;
				background-color: #efeae8;
				@include qodeBoxShadow(none);
				@include qodeBorderRadius(5px);

				.rangeslider__fill{
					background-color: $first-main-color;
					@include qodeBoxShadow(none);
					@include qodeBorderRadius(5px);
				}

				.rangeslider__handle{

					height: 14px;
					width: 14px;
					top: -4px;
					background-color: $first-main-color;
					background-image: none;
					border: none;
					box-shadow: none;

					&:after{
						display: none;
					}
				}
			}
	}

	.qode-irc-content-holder{
		.qode-irc-content-row{
			padding: 17px 0;
			border-bottom: 1px solid #efeae8;

			.qode-irc-label{
				float: left;
				font-size: 19px;
				font-weight: 400;
			}

			.qode-irc-value-holder{
				float: right;
				font-size: 23px;
				color: #1c1c1c;
			}

			&.qode-irc-total-row{
				.qode-irc-value-holder{
				color: $first-main-color;
				}
			}
		}
	}

	.qode-irc-button-holder{
		margin-top: 49px;
		.qbutton.big_large_full_width{
			@include qodeBoxSizing(border-box !important);
		}
	}
}