/* Twitter Feed - begin
========================================================================== */
.qode-twitter-feed-shortcode{
	overflow: hidden;

	.qode-tfs-inner{

		margin: -33px -25px;

		.qode-tfs-item {

			width: 33.33%;
			float: left;

			&:nth-child(3n+1) {
				clear: both;
			}

			.qode-tfs-item-inner {
				padding: 33px 25px;
				box-sizing: border-box;
			}
		}
		.qode-tfs-image-info-holder {
			display: table;
			margin-bottom: 13px;
		}
		.qode-tfs-info {
			line-height: 1em;
			margin-top: 3px;
		}
		.qode-tfs-image {
			display: table-cell;
			width: 60px;

			img {
				vertical-align: middle;
			}

		}
		.qode-tfs-info-holder {
			display: table-cell;
			vertical-align: middle;
			padding-left: 20px;
			font-size: 0.8em;
			text-align: left;
		}

		.qode-tfs-time {
			padding-left: 3px;
		}

		.qode-tfs-text {
			text-align: left;

			* {
				color: inherit;
			}
		}
	}

}

ul.qode_twitter_widget li {
	margin-bottom: 10px;
}

/* Twitter Feed - end
========================================================================== */