/* ==========================================================================
   Pricing Calculator shortcode start styles
   ========================================================================== */
.qode-sliding-image-holder {

	position: relative;

	.qode-sih-image-holder {
		position: absolute;
		overflow: hidden;
		background-size: cover;
		background-repeat: no-repeat;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		z-index: -1;

		.qode-sliding-image-background-image {
			position: relative;
			max-width: 360%;

			&.qode-aux-background-image {
				position: absolute;
				top: 0;
				left: 360%;
			}
		}
	}

}


/* ==========================================================================
   Pricing Calculator shortcode end styles
   ========================================================================== */