.blog_holder.blog_pinterest {
    margin-bottom: 20px;
    transition: none !important;
    overflow: visible !important;
    opacity: 0;

    .blog_holder_grid_sizer{
        width: 31.7%;
    }

    .blog_holder_grid_gutter{
        width: 2.3%;
    }

    .mejs-container,
    .mejs-mediaelement{
        border: none;
    }

    article {
        display: block;
        float: left;
        margin: 0 0 30px;
        padding: 0;
        text-align: left;
        vertical-align: top;
        z-index: 100;
        border: 0;
        width: 31.7%;
        .entry_title:hover {
            text-decoration: underline;
        }

        .blog_holder_grid_gutter{
            width: 3%;
        }

        .post_text .post_text_inner{
            padding: 22px 30px 25px;
            background-color:#fff;
        }

        &.format-link,
        &.format-quote{
            .post_text .post_text_inner {
                padding: 50px 45px;
                background-color: #fff !important;
                text-align: center;

                .icon{
                    font-size: 55px;
                    color: #e0e0e0;
                    margin-bottom: 30px;
                    display: block;
                }

                h5 span{
                    font-size: 0.8em;
                }
            }

            .quote_author{
                margin-top: 12px;
                text-transform: uppercase;
                font-size: 10px;
                color: #2e2e2e !important;
                font-weight: 600;
            }

        }

        &.format-audio{
            .post_text .post_text_inner {
                padding-top: 8px;
            }
        }

        .post_image{
            margin: 0;
            width: auto;
        }

        .post_info{
            margin-bottom: 10px;
        }

        .post_info,
        .post_info a:not(:hover){
            font-size: 10px;
            color:#2e2e2e;
            text-transform: uppercase;
        }

        h5 a{
            color: #2e2e2e;
            font-size: 23px;
            line-height: 26px;
            text-transform: none;
            margin: 0px;
        }
    }

    .two_columns_75_25 &,
    .two_columns_25_75 &,
    .two_columns_66_33 &,
    .two_columns_33_66 &{
        article,
        .blog_holder_grid_sizer{
            width:48%;
        }
        .blog_holder_grid_gutter{
            width: 4%;
        }

    }

}