/* ==========================================================================
   Image Gallery shortcode style - begin
   ========================================================================== */

.qode-advanced-image-gallery {
	@include qodeRelativeHolderLayout();
	
	&.qode-has-shadow {
	
		.qode-aig-image-inner {
			box-shadow: $default-box-shadow;
		}
		
		&.qode-aig-slider-type,
		&.qode-aig-carousel-type {
			
			.owl-stage-outer {
				padding: 0 0 20px;
			}
			
			.qode-aig-image {
				box-shadow: $default-box-shadow;
			}
		}
	}
	
	.qode-aig-image {
		@include qodeRelativeHolderLayout();
		box-sizing: border-box;
		
		a, img {
			position: relative;
			display: block;
		}
	}
	
	.qode-aig-image-inner {
		@include qodeRelativeHolderLayout();
	}
	
	.qode-aig-slider {
		@include qodeRelativeHolderLayout();
	}
	
	/***** Image Gallery Grid Style - begin *****/
	
	.qode-aig-grid {
		
		.qode-aig-image {
			float: left;
		}
		
		$gallery_columns: ('two', 'three', 'four', 'five', 'six');
		@for $i from 0 to length($gallery_columns) {
			&.qode-aig-#{nth($gallery_columns, $i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.qode-aig-image {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/***** Image Gallery Grid Style - end *****/
	
	/***** Image Gallery Masonry Style - begin *****/
	
	.qode-aig-masonry {
		opacity: 0;
		@include qodeTransition(opacity .2s ease-out);
		
		.qode-aig-grid-sizer {
			width: 100%;
		}
		
		.qode-aig-grid-gutter {
			width: 0;
		}
		
		$masonry_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_columns) {
			&.qode-aig-#{nth($masonry_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.qode-aig-image,
				.qode-aig-grid-sizer {
					width: $column_width;
				}

				.qode-aig-image.qode-aig-large-masonry-item,
				.qode-aig-image.qode-aig-large-width-masonry-item {
					width: $column_width * 2;
				}
			}
		}
	}
	
	/***** Image Gallery Masonry Style - end *****/
	
	/***** Custom Link Behavior Style - begin *****/
	
	&.qode-image-behavior-custom-link {
		
		.qode-aig-image {
			
			a {
				@include qodeImageOverlayHoverStyle();
			}
		}
	}
	
	/***** Custom Link Behavior Style - end *****/
	
	/***** Lightbox Behavior Style - begin *****/
	
	&.qode-image-behavior-lightbox {
		
		.qode-aig-image {

			a{
			
				.aig-gallery-hover{
				
					@include qodeAbsoluteHolderLayout();
					background-color: rgba(53,53,53,.4);
					opacity: 0;
					@include qodeTransition(opacity .2s ease-in-out);

					span{
						position: absolute;
						left: 50%;
						top: 50%;
						width: 40px;
						height: 40px;
						font-size: 40px;
						margin: -20px 0 0 -20px;
						line-height: 40px;
						color: #fff;
					}
				}

				&:hover{
					.aig-gallery-hover{
						opacity: 1;
					}
				}
			}
		}
	}
	
	/***** Lightbox Behavior Style - end *****/
	
	/***** Zoom Behavior Style - begin *****/
	
	&.qode-image-behavior-zoom {
		
		.qode-aig-image {
			
			.touchevents & {
				cursor: pointer;
			}
			
			&:hover {
				
				img {
					@include qodeTransform(scale(1.04));
				}
			}
			
			.qode-aig-image-inner {
				overflow: hidden;
			}
			
			img {
				@include qodeTransform(scale(1));
				@include qodeTransitionTransform(.3s ease-in-out);
			}
		}
	}
	
	/***** Zoom Behavior Style - end *****/
	
	/***** Grayscale Behavior Style - begin *****/
	
	&.qode-image-behavior-grayscale {
		
		.qode-aig-image {
			overflow: hidden;
			
			.touchevents & {
				cursor: pointer;
			}
			
			&:hover {
				
				img {
					-webkit-filter: grayscale(0);
					filter: none;
				}
			}
			
			img {
				filter: url('img/desaturate.svg#grayscale');
				-webkit-filter: grayscale(100%);
				-moz-filter: grayscale(100%);
				filter: gray;
				filter: grayscale(100%);
				@include qodeTransition(all .3s ease-in-out);
			}
		}
	}

	/***** Grayscale Behavior Style - end *****/

	.owl-nav {
		
		&.disabled {
			display: none;
		}
		
		.owl-prev,
		.owl-next {
			cursor: pointer;
			cursor: hand;
			-webkit-user-select: none;
			-khtml-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
		}

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			@include qodeTransform(translateY(-50%));

			&:hover {

				.qode-prev-icon,
				.qode-next-icon {
					color: $first-main-color;
				}
			}
		}

		.owl-prev {
			left: 30px;
		}

		.owl-next {
			right: 30px;
		}

		.qode-prev-icon,
		.qode-next-icon {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			color: #fff;
			@include qodeTransition(color .2s ease-out);
			font-size: 30px;
			line-height: 1em;
			width: 54px;
			height: 54px;
			line-height: 54px;
			border: 2px solid #303030;
			text-align: center;
			@include qodeBorderRadius(50%);

			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;

		&.disabled {
			display: none;
		}
		
		.owl-dot {
			cursor: pointer;
			cursor: hand;
			-webkit-user-select: none;
			-khtml-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
		}

		.owl-dot {
			display: inline-block;
			vertical-align: middle;

			span {
				display: inline-block;
				vertical-align: middle;
				width: 9px;
				height: 9px;
				margin: 0 6px;
				@include qodeBorderRadius(50%);
				background-color: #cfcfcf;
				border: none;
				@include qodeTransition(background-color .2s ease-out, border .2s ease-out);
				box-sizing: border-box;
			}

			&:hover,
			&.active {

				span {
					background-color: #303030;
				}
			}
		}
	}
	
}
/* ==========================================================================
   Image Gallery shortcode style - end
   ========================================================================== */
