/* ==========================================================================
   #Comparative Features Table style - start
   ========================================================================== */
.qode-comparative-features-table{

	$cft-border-color: #e3e3e3;

	border-collapse: collapse;
	box-sizing: border-box;
	border: 1px solid $cft-border-color;


	.qode-cft-row {

		display: table;
		width: 100%;
		table-layout: fixed;
		border-bottom: 1px dashed $cft-border-color;
		border-collapse: initial;
		> * {
			display: table-cell;
			vertical-align: middle;
			box-sizing: content-box;
		}

		&:last-child {
			border: none;
		}

		> * {

			border-right: 1px solid $cft-border-color;

			&:last-child {
				border: none;
			}
		}

		&:nth-child(odd) {
			background-color: #fff;
		}
		&:nth-child(even) {
			background-color: #f6f6f6;
		}
	}

	.qode-cft-title-holder,
	.qode-cft-feature-title,
	.qode-cft-link-holder
	{
		width: 55%;
		padding: 23px 30px;
	}
	.qode-cft-column-title-holder,
	.qode-cft-feature-value,
	.qode-cft-column-link-holder {
		width: 15%;
		text-align: center;
		padding: 23px 0;
	}

	&.qode-cft-two-columns {
		.qode-cft-title-holder,
		.qode-cft-feature-title,
		.qode-cft-link-holder
		{
			width: 60%;
			padding: 23px 30px;
		}
		.qode-cft-column-title-holder,
		.qode-cft-feature-value,
		.qode-cft-column-link-holder {
			width: 20%;
			text-align: center;
		}
	}

	&.qode-cft-one-column {
		.qode-cft-title-holder,
		.qode-cft-feature-title,
		.qode-cft-link-holder
		{
			width: 70%;
			padding: 23px 30px;
		}
		.qode-cft-column-title-holder,
		.qode-cft-feature-value,
		.qode-cft-column-link-holder {
			width: 30%;
			text-align: center;
		}
	}

	.qode-cft-footer {
		table-layout: auto;
		width: 100%;

		&.qode-cft-row {
			> * {
				border: none;
			}
		}

	}

	.qode-cft-column-footer-image {
		padding: 21px 30px;

		img {
			vertical-align: middle;
		}
	}
	.qode-cft-column-footer-text {
		text-align: right;
		padding-right: 45px;
	}

	.qode-cft-feature-item-title-responsive {
		vertical-align: middle;
		display: none;
	}

	.qode-cft-mark {
		font-size: 25px;
		vertical-align: middle;

		&.qode-cft-active {
			color: $first-main-color;
		}
		&.qode-cft-inactive {
			color: #a9a9a9;
		}
	}

	.qode-cft-link {
		font-size: 13px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1px;
		color: #303030;

		&:hover {
			color: $first-main-color;
		}
	}
}
/* ==========================================================================
   #Comparative Features Table style - end
   ========================================================================== */