/* Content Slider - begin
========================================================================== */
.qode_content_slider{
    overflow: hidden;
    position: relative;

    .qode_content_slider_inner{
        padding-left: 1px; //beacuse content is cut on the left side
    }

    .flex-control-nav {
        text-align: center;

        li {
            display: inline-block;
            width: 9px;
            height: 9px;
            margin: 0 5px;

            a {
                display: block;
                width: 100%;
                height: 100%;
                text-indent: -9999px;
                background-color: #cfcfcf;
                border-radius: 50%;

                &.flex-active {
                    background-color: #303030;
                }
            }
        }
    }

    .flex-direction-nav{
        a {
            border: medium none;
            @include qodeTransition(background-color 0.2s ease);
        }

        i{
            @include qodeTransition(color 0.2s ease);
        }


        .flex-prev{
            border-radius: 2em;
            left: 15px;
        }
        .flex-next {
            border-radius: 2em;
            right: 15px;
        }

        .flex-prev:hover,
        .flex-next:hover{
            background-color: #fff;

            i{
                color:#8a8a8a;
            }
        }
    }

    &.has_control_nav{
        .flex-direction-nav a{
            -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
        }
    }

    &.drag_enabled{
        cursor: e-resize;
        cursor: -webkit-grab;
        cursor: grab;
    }

    &.control_nav_justified {
        overflow: visible;

        .flex-control-nav {
            position: relative;
            margin:auto;

            &:after {
                content:'';
                position:absolute;
                top: calc(50% - 1px);
                left:0;
                width:100%;
                height:2px;
                z-index:-1;
            }

            li {
                position: relative;
                margin: 0;
                vertical-align: middle;
                height: auto;
                width: auto;
                @include qodeBorderRadius(50%);

                a {
                    width: 16px;
                    height: 16px;
                    background-color: #fff !important; //override active state background color change
                    @include qodeBoxShadow(0px 1px 2px 2px rgba(0,0,0,0.1));
                    @include qodeTransition(all .3s cubic-bezier(0.18, 0.89, 0.32, 1.48));

                    &:after {
                        content:'';
                        position: absolute;
                        width: 6px;
                        height: 6px;
                        top:50%;
                        left:50%;
                        @include qodeTransform(translate(-50%,-50%));
                        @include qodeBorderRadius(50%);
                        background-color: #cfcfcf;
                    }

                    &.flex-active {
                        @include qodeTransform(scale(1.3));
                        @include qodeBoxShadow(0px 1px 2px 2px rgba(0,0,0,0.12));
                    }
                }
            }
        }
    }
}
/* Content Slider - end
========================================================================== */