.blog_holder.single_image_title_post {
	article {
		.post_content_holder {
			position: relative;
		}
		.post_image {
			text-align: center;
		}
		.single_top_part_holder {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			text-align: center;
			background-color: rgba(0,0,0,0.3);

		}
		.single_top_part {
			display: table;
			height: 100%;
			width: 100%;
		}
		.single_top_part_inner {
			display: table-cell;
			height: 100%;
			vertical-align: middle;
		}
		.qode-itp-single-separator.separator {
			width: 64px;
			height: 4px;
			margin-top: 35px;
			margin-bottom: 35px;
		}
		.post_info {
			text-transform: uppercase;
			font-size: 10px;
			font-weight: 500;
			letter-spacing: 3px;
			display: block;
			color: #fff;

			a {
				color: #fff;

				&:hover span {
					color: #fff;
				}
			}
		}

		.post_category {

			text-transform: uppercase;
			font-size: 10px;
			font-weight: 500;
			letter-spacing: 3px;
			display: block;
			margin-bottom: 17px;

			a {
				color: #fff;
				@include qodeTransition(color 0.2s ease-out, border-color 0.2s ease-out);
			}
		}

		h1 {
			color: #fff;
		}

		.itp_post_text {
			.post_text_inner {
				padding: 95px 0 0 0;
			}
		}

		.single_bottom_part {

			display: inline-block;
			vertical-align: middle;
			width: 100%;
			padding: 80px 0 30px;
			border-bottom: 1px solid #e5e5e5;

			.single_bottom_part_left {
				width: 50%;
				float: left;
				text-align: left;

				.single_tags {
					margin: 0;

				}
				.tags_text h5 {
					display: none;
				}
			}
			.single_bottom_part_right {
				width: 50%;
				float: left;
				text-align: right;

				.social_share_list_holder > span {
					display: none;
				}
			}

			.social_share_list_holder ul li i {

			}
		}
		&.format-link,
		&.format-quote {


		}
	}
	.comment_holder {
		padding-left: 10%;
		padding-right: 10%;
	}
	.comment_form {
		box-sizing: border-box;
		padding-left: 10%;
		padding-right: 10%;
	}
}