/* ==========================================================================
   Accordion shortcode start styles
   ========================================================================== */
.qode-accordion-holder{
    position: relative;

    .qode-accordion-content{
        margin: 0;
        padding: 20px;
        background-position: center;
        background-size: cover;

        &.qode-acc-title-with-icon{
        	padding-left: 60px;
        }
    }

    .qode-title-holder {
        cursor: pointer;
        position: relative;
        padding: 0;
        box-sizing: border-box;
        background-color: #fff;
        border-bottom: 1px solid #f4f4f4;
        @include qodeTransform(translateZ(0px));
        @include qodeTransition(color .15s ease-out, background-color .15s ease-out, border-color .15s ease-out);

        &:last-of-type{
        	border-bottom-color: transparent;
        }

        .qode-tab-title,
        .qode-tab-title-inner{
            color: inherit;
            line-height: inherit;
        }

        .qode-tab-title{
            display: inline-table;
            table-layout: fixed;
            height: 66px;
            width: calc(100% - 41px);
            box-sizing: border-box;
            text-align: left;
            table-layout: fixed;

            span.qode-tab-title-inner{
                position: relative;
                display: table-cell;
                vertical-align: middle;
                text-align: left;
                padding: 5px 0;

                &:only-child{
                	padding-left: 20px;
                	width: 100%;
                }
            }

            .qode-tab-title-icon{
            	display: table-cell;
                vertical-align: middle;
            	text-align: center;
            	width: 60px;
            	font-size: 26px;
            	padding: 0 10px;
            	box-sizing: border-box;
            }
        }

        .qode-accordion-mark {
        	position: absolute;
        	top: 0;
        	right: 6px;
            display: inline-block;
            vertical-align: top;
            width: 30px;
            height: 100%;
            color: inherit;
            box-sizing: border-box;
        }

        .qode-accordion-mark-icon {
            display: block;
            width:100%;
            height: 100%;
            font-size: 21px;
            line-height:inherit;
            text-align: center;
        }

        .qode-accordion-mark-icon span{
            position: absolute;
            display: block;
            top: 50%;
            width: 100%;
            line-height:inherit;
            margin-left: -1px;
            @include qodeTransition(opacity .2s);
            @include qodeTransform(translateY(-50%));

            &:last-child{
            	opacity: 0;
            }
        }

    }

    .qode-title-holder.ui-state-active,
    .qode-title-holder.ui-state-hover{
        color: #fff;
        background-color: $first-main-color;
    }

    .qode-title-holder.ui-state-active{
        border-bottom-color: transparent;

        .qode-accordion-mark-icon {
            span:first-child{
            	opacity: 0;
            }

            span:last-child{
            	opacity: 1;
            }
        }
    }
}