.no-touchevents nav.vertical_menu_on_click ul > li.menu-item-has-children.open > a > .plus,
.no-touchevents nav.vertical_menu_float ul > li.menu-item-has-children.open > a > .plus{
    background-image: url('img/vertical_menu_minus.png');
}

.touchevents nav.vertical_menu_toggle ul > li.menu-item-has-children.open > a > .plus,
.touchevents nav.vertical_menu_on_click ul > li.menu-item-has-children.open > a > .plus,
.touchevents nav.vertical_menu_float ul > li.menu-item-has-children.open > a > .plus {
    background-image: url('img/vertical_menu_minus.png');
}

nav.vertical_menu_toggle ul li.menu-item-has-children a span,
nav.vertical_menu_on_click ul li.menu-item-has-children a span,
nav.vertical_menu_float ul li.menu-item-has-children a span{
    display: inline-block;
    max-width: 180px;
    line-height: 1em; /* this is for undeline dash, because sthis span needs to be exactly around text since dash is -50% bottom */
}

.vertical_menu_float .menu-item .second {
    position: absolute;
    top: 0;
    left: calc(100% + 30px); /*because of the padding*/
    width: 100%;
    margin-left: 50px;
    opacity: 0;
    background-color: #fff;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

.vertical_menu_float .menu-item .second *{
    visibility: hidden;
    transition: visibility 0.3s ease-in-out;
}

.vertical_menu_float .menu-item .second.vertical_menu_start {
    margin-left: 0px;
    opacity: 1;
    visibility: visible;
}

.vertical_menu_float .menu-item .second.vertical_menu_start *{
    visibility: visible;
}


.vertical_menu_float .menu-item .second.vertical_menu_start ul li ul,
.vertical_menu_float .menu-item .second.vertical_menu_start ul li ul *{
    visibility:hidden;
}

.vertical_menu_float .menu-item .second.vertical_menu_start ul li ul.vertical_submenu_start,
.vertical_menu_float .menu-item .second.vertical_menu_start ul li ul.vertical_submenu_start *{
    visibility:visible;
}

.vertical_menu_float .second .inner ul{
    width: 100%;
}

.vertical_menu_float li.narrow .second .inner ul{ /*because of the .narrow styling*/
    border: none;
    background-color: #fff;
    padding: 0;
}

.vertical_menu_float .second .inner ul li ul {
    position: absolute;
    border:none;
    top: 0;
    left: 100%;
    margin-left: 50px;
    height: auto;
    visibility: hidden;
    width: 100%;
    opacity: 0;
    overflow: hidden;
    z-index: 10;
    background-color: #fff;
    padding: 0;
    transition: all 0.3s ease-in-out;
}

.vertical_menu_float .second .inner ul li ul.vertical_submenu_start{
    opacity: 1;
    margin-left:0;
    visibility: visible;
}

