/* ==========================================================================
   Report Sheet shortcode start styles
   ========================================================================== */
.qode-report-sheet{

	position: relative;
	padding: 70px 56px 116px;
	margin-bottom: 31px;
    background-color: white;

	
	.qode-rs-title-holder{

		margin-bottom: 27px;

	}


$columns_number: ('one-column','two-columns', 'three-columns', 'four-columns', 'five-columns');
	
	@for $i from 0 to length($columns_number) {
		&.qode-rs-#{nth($columns_number,$i+1)} {
			$column_width: 100%/($i+2);
			
			.qode-rs-table{
				.qode-rs-table-header,
				.qode-rs-table-content{

					.qode-rs-table-column{
						width: $column_width; 
						float: left;

						&:first-child{
							width: 2*$column_width;
						}
					}

				}
			}
			
		}
	}

	.qode-rs-table{
		.qode-rs-table-header{
			padding: 10px 0;
			border-bottom: 1px solid $first-main-color;

			.qode-rs-column-title-holder{
				.qode-rs-column-title{
					text-transform: none;
				}
			}
		}

		.qode-rs-table-content {
			.qode-rs-table-row{
				padding: 11px 0;
    			border-bottom: 1px solid $first-main-color;

    			.qode-rs-table-column-title-header{
    				display: none;
    			}

    			.qode-rs-table-column-title{
    				font-size: 16px;
    				font-weight: 600;
    				color: #28282a;
    			}

    			.qode-rs-table-column-subtitle{
    				font-size: 12px;
    				color: #a0a0a1;
    				text-transform: uppercase;
    			}
			}
		}
	}

	.qode-rs-button-holder {
		position: absolute;
    	right: 0;
    	top: 100%;
    	@include qodeTransform(translateY(-50%));

    	a{
    		text-transform: none;
    	}
	}


}