.qode-workflow{
    margin-top: 50px;
    position: relative;

    .main-line{
        background: #fff;
        left: 50%;
        margin-left: -1px;
        position: absolute;
        right: 50%;
        top: 0;
        height: 100%;
        width: 2px;
    }

    .qode-workflow-item{
        margin-left: auto;
        margin-right: auto;
        max-width: 75%;
        position: relative;
        padding-bottom: 50px;
        overflow: hidden;

        .qode-workflow-item-inner{
            display: inline-block;
            position: relative;
            width: 100%;
            vertical-align: middle;

            .qode-workflow-image,
            .qode-workflow-text{
                float: left;
                margin: 0;
                width: 50%;

				@include qodeBoxSizing(border-box);
            }

            .qode-workflow-image{
                text-align: center;

                &.left{
                    padding: 0px 60px;
                    text-align: left;
                }
                &.right{
                    padding: 0px 60px;
                    text-align: right;
                }
            }

            .qode-workflow-text{
                padding: 0px 60px;

                h4{
                    margin-top: 0;
                    margin-bottom: 0px;
                }

                p.text{
                    margin-top: 25px;
                }

                .circle {
	                box-sizing: border-box;
                    background: #fff;
	                border: 1px solid transparent;
                    border-radius: 50%;
                    content: "";
                    height: 18px;
                    left: 50%;
                    margin: 0 0 0 -9px;
                    position: absolute;
                    top: 0;
                    width: 18px;
                }
            }

            .qode-workflow-subtitle{
            	margin-top: 5px;
            }

            &.reverse{
                display: -webkit-box;
                display: -webkit-flex;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-orient: horizontal;
                -webkit-box-direction: reverse;
                -webkit-flex-direction: row-reverse;
                -ms-flex-direction: row-reverse;
                flex-direction: row-reverse;
                -webkit-flex-wrap: wrap;
                -ms-flex-wrap: wrap;
                flex-wrap: wrap;


            	.qode-workflow-text{
            		text-align: right;
            	}
            }

        }

        .line{
            display: none;
            background-color: #fff;
            height: 100%;
            left: 50%;
            margin-left: -1px;
            position: absolute;
            width: 2px;
        }

        &:first-of-type .line{
            display: block;
            top: 4px;
            transform: translateY(-100%);
        }

        &:last-of-type .line{
            display: block;
            top: 24px;
        }

    }

    &.qode-workflow-animate {
        @include qodeTransform(translateY(100px));
        opacity:0;
        -webkit-transition: opacity .55s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform .55s cubic-bezier(0.23, 1, 0.32, 1);
        transition: opacity .55s cubic-bezier(0.23, 1, 0.32, 1), transform .55s cubic-bezier(0.23, 1, 0.32, 1);
        .main-line{
            opacity: 0;
            height: 0;
            @include qodeTransition(opacity .55s cubic-bezier(0.23, 1, 0.32, 1), height 1.8s ease-out);
        }
        .circle {
            @include qodeTransform(scale(.2));
            -webkit-transition: -webkit-transform .6s cubic-bezier(0.18, 0.89, 0.32, 1.68) .5s;
            transition: transform .6s cubic-bezier(0.18, 0.89, 0.32, 1.68) .5s;
        }
        .qode-workflow-item {
            .qode-workflow-item-inner {
                .qode-workflow-image {
                    opacity:0;
                    @include qodeTransform(scale(.6));
                    -webkit-transition: -webkit-transform .6s cubic-bezier(0.18, 0.89, 0.32, 1.28), opacity .3s ease-out;
                    transition: transform .6s cubic-bezier(0.18, 0.89, 0.32, 1.28), opacity .3s ease-out;
                }
                .qode-workflow-text {
                    h4, p {
                        opacity:0;
                        @include qodeTransition(opacity .5s cubic-bezier(0.22, 0.61, 0.36, 1) .2s);
                    }
                }
            }   
        }
        &.qode-appeared {
            @include qodeTransform(translateY(0));
            opacity:1;
            .main-line {
                opacity: 1;
                height: 100%;
            }
            .qode-workflow-item.qode-appeared{
                .qode-workflow-image {
                    opacity:1;
                    @include qodeTransform(scale(1));
                }
                .qode-workflow-text {
                    h4,p {
                        opacity:1;
                    }
                }

            }
            .circle {
                @include qodeTransform(scale(1));
            }
        }
    }
}