/* ==========================================================================
   elliptical Slider shortcode start styles
   ========================================================================== */
.qode-elliptical-slider{
	.qode-elliptical-slide {
		position: relative;
		display: inline-block;
		background-size: cover;
		background-position: center;
		width: 100%;
		vertical-align: middle;

		.qode-elliptical-slide-image-holder-wrapper {
			line-height: 0;
		}

		.qode-elliptical-slide-image-holder {
			display: block;

			img {
				width: 100%;
				height: auto;
				max-height: 900px;
			}
		}

		.qode-elliptical-slide-content-holder {
			position:absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: -webkit-linear-gradient(left, rgba(41,41,41,1) 50%, transparent 50%);
			background: linear-gradient(90deg, rgba(41,41,41,1) 50%, transparent 50%);
		}

		.qode-elliptical-slide-content-holder-inner {
			width: 100%;
			height: 100%;
			display: inline-block;
		}

		.qode-elliptical-slide-content-wrapper {
			height: 100%;

			.qode-elliptical-slide-wrapper-inner {
				width: 50%;
				height: 100%;
				box-sizing: border-box;
				display: table;
				position: relative;
			}

			.qode-elliptical-slide-elements-holder {
				display: table-cell;
				vertical-align: middle;
				padding-right: 50px;
			}
		}

		.qode-elliptical-slide-svg-holder {
			position: absolute;
			height: 100%;
			right: 0;
			top: 0;

			svg {
				width: auto;
				height: 100%;
				vertical-align: middle;
				position: relative;
				left: 100%;
				left: calc(100% - 2px);
				path {
					fill: rgba(0,0,0,1);
				}
			}
		}

	}

	.flex-control-nav.flex-control-paging {
		position: absolute;
		left: 50px;
		top: 50%;
		z-index: 100;
		@include qodeTransform(translateY(-50%));

		li {
			padding: 3px 0;

			a{
				background-color: rgba(255,255,255,0.4);
				border-radius: 100%;
				display: block;
				width: 10px;
				height: 10px;
				text-indent: -9999px;

				&.flex-active {
					background-color: rgba(255,255,255,1);
				}
			}
		}
	}
}



/* ==========================================================================
   elliptical Slider shortcode end styles
   ========================================================================== */