/* ==========================================================================
Expanding Images shortcode styles - start
========================================================================== */
.qode-expanding-images {
    position: relative;
    text-align: center;

    .qode-expanding-images-inner {
        display: inline-block;
        position: relative;
        max-width: 46.875%;
    }

    .qode-frame-image {
        position: relative;
        z-index: 30;
        pointer-events:none; 
    }

    &.qode-ie-specific {
        .qode-frame-image {
            z-index: 10;
        }
    }

    .qode-hero-image {
        position: absolute;
        height: 66%;
        width: 92%;
        left: 4%;
        top: 5%;
        z-index: 20;
        overflow: hidden;
        background-color: #f3f3f3;
        
        .qode-hero-image-link {
            display: block;
            position: relative;
            @include qodeTransform(translate3d(0,0,0));
            height: 100%;
            
            &:after {
                content:'';
                position: absolute;
                top:0;
                left:0;
                height:100%;
                width:100%;
                background-color:rgba(255,255,255,.12);
                opacity:0;
                @include qodeTransition(opacity .4s cubic-bezier(0.21, 0.74, 0.52, 0.87));
            }
            &:hover {
                &:after {
                    opacity:1;
                }

                > .qode-hero-image-title {
                    @include qodeTransform(translate3d(0, 0,0));    
                    @include qodeTransition(all .3s ease);
                }

                img{
                    @include qodeTransform(translate3d(0,-30px,0));   
                    @include qodeTransition(.35s cubic-bezier(.18,.87,.28,1.01) .05s); 
                }
            }
        }

        img {
            @include qodeTransition(all .3s ease);
            will-change: transform;
        }
    }

    .qode-hero-image-title {
        position: absolute;
        width: 100%;
        bottom:0;
        background:#fff;
        z-index:300;
        padding: 17px 0;
        @include qodeTransition(all .35s ease);
        @include qodeTransform(translate3d(0,100%,0));    

        h3 {
            text-transform: none;
            font-size: 14px;
            font-weight: 500;
        }
    }

    .qode-side-images {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 77%;
    }

    .qode-side-image-link {
        @include qodeTransition(opacity .4s cubic-bezier(0.21, 0.74, 0.52, 0.87));
        &:hover {
            opacity: .95;
        }
    }

    .qode-side-image {
        position: absolute;
        background-size:cover;
        background-position: center center;
        background-repeat: no-repeat;
        @include qodeTransition(all 1s cubic-bezier(0.21, 0.74, 0.52, 0.87));
        @include qodeBoxShadow(3px 3px 6px 0px rgba(0,0,0,0.1));

        @for $i from 1 through 8 {
            &:nth-of-type(#{$i}) { 
                z-index: 8 - $i;
            }
        }

        &.qode-side-image-1,
        &.qode-side-image-3 {
            width: 16.92%;
            padding-top:  10.575%; //aspect ratio 1.6
        }

        &.qode-side-image-2,
        &.qode-side-image-4 {
            width: 17.7%;
            padding-top:  11.0625%; //aspect ratio 1.6
        } 

        &.qode-side-image-1 {
            top: 7%;
            left: 13.5%;
        }

        &.qode-side-image-2 {
            bottom: 9.5%;
            left: 10.3%;
        }

        &.qode-side-image-3 {
            top: 7%;
            right: 14%;
        }

        &.qode-side-image-4 {
            bottom: 9.5%;
            right: 9.8%;
        }

        &.qode-side-image-5,
        &.qode-side-image-7 {
            width: 11.56%;
            padding-top: 7.225%; //aspect ratio 1.6
        }

        &.qode-side-image-6,
        &.qode-side-image-8 {
            width: 10.88%;
            padding-top: 6.8%; //aspect ratio 1.6
        }

        &.qode-side-image-5 {
            top: 12.2%;
            left: 0.5%;
        }

        &.qode-side-image-7 {
            top: 12.2%;
            right: 0.5%;
        }

        &.qode-side-image-6 {
            bottom: 24.5%;
            left: -3%;
        }

        &.qode-side-image-8 {
            bottom: 24.5%;
            right: -3%;
        }
    }

}
/* ==========================================================================
Expanding Images shortcode styles - end
========================================================================== */