.blog_headlines {
	margin-bottom: 56px;

	article {
        margin-bottom: 40px;
        text-align: center;

        h2 {
            line-height: 1em;
            opacity: 0;
            @include qodeTransform(translateY(50px) scale(0.7));
            @include qodeTransition(opacity 0.6s ease, transform 0.6s ease);

            &.show{
                opacity: 1;
                @include qodeTransform(translateY(0px) scale(1));
            }

            a{
                font-size: inherit; // This is added in order to prevent global font option for H2 to override calculated font size
                line-height: 1em;
            }
        }
    }
}