/* ==========================================================================
   Multi-Device Showcase start styles
   ========================================================================== */
#qode-multi-device-showcase {
	position: relative;
  	height: 100vh;
  	width: 100vw;
  	z-index: 1000;
  	
  	&.qode-mds-overflow-hidden {
	  	overflow: hidden;
  	}

  	//content
  	.qode-mds-content-holder {
  		display: flex;
  		align-items: center;
  		justify-content: center;
  		height: 100%;
  		width: 35%;
  		box-sizing: border-box;
  		padding-left: 2.5%;

  		.qode-mds-title-holder {
  			margin-bottom: 10px;
  		}

  		.qode-mds-subtitle-holder {
  			margin-bottom: 20px;
 		}		
  	}

  	//global devices
  	.qode-mds-device-holder {
		position: absolute;
	    @include qodeTransform(rotate(-37deg));

	    &.qode-mds-laptop-holder {
		    width: 35%;

		    &.qode-mds-additional-element {
		    	top: 33%;
	    	    left: 84%;
		    }
	    }

        &.qode-mds-tablet-portrait-holder {
    	    width: 17%;

    	    &.qode-mds-additional-element-1 {
    	    	top: 12%;
	    	    left: 69%;
    		}

    	    &.qode-mds-additional-element-2 {
	    		top: -27%;
	    		left: 21%;
	    	}

    	    &.qode-mds-additional-element-3 {
    	    	top: 79%;
    	    	left: 58%;
	    	}
        }

        &.qode-mds-tablet-landscape-holder {
    	    width: 20%;

    	    &.qode-mds-additional-element {
	    	    &.qode-mds-additional-element-1 {
    	    	    top: -12%;
	    	    	left: 51%;
	    		}

	    	    &.qode-mds-additional-element-2 {
	    	    	top: -6%;
	    	    	left: 84%;
		    	}
    	    }
        }

        &.qode-mds-phone-portrait-holder {
        	top: 70%;
        	left: 32%;
    	    width: 10%;
        }

        &.qode-mds-laptop-slider-holder {
    	    top: 42%;
        	left: 38%;
		    width: 35%;
        }

		&.qode-mds-tablet-slider-holder {
			top: 5%;
			left: 35%;
			width: 17%;
		}

		&.qode-mds-phone-slider-holder {
			top: 63%;
			left: 77%;
    	    width: 10%;
		}
  	}

	.qode-mds-device-frame {   
		display: block;
		-webkit-backface-visibility: hidden;
	}

  	.qode-mds-slide {
		@include qodeAbsoluteHolderLayout();
		pointer-events: none;
		visibility: hidden;

		&.qode-active {
			pointer-events: auto;
			visibility: visible;
		}

		&.qode-remove {
	  		visibility: visible;
		}
  	}

  	.qode-mds-slide-link {
		@include qodeAbsoluteHolderLayout();
		z-index: 100;
    }

  	.qode-mds-slide-image {
		@include qodeAbsoluteHolderLayout();
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
  	}

  	.qode-mds-device-image {
  		background-size: cover;
  		background-repeat: no-repeat;
  		background-position: center;
  		background-color: #ccc;
  	}

  	.qode-mds-slides-inner {
		@include qodeAbsoluteHolderLayout();
		background-color: #ccc;
  	}

  	//laptop slider
  	#qode-mds-laptop-slider {
		position: relative;

		.qode-mds-laptop-slides {
			position: absolute;
			top: 8.7%;
			left: 16.4%;
			height: 69.1%;
			width: 67.7%;
			overflow: hidden;
		}
  	}	 

  	//tablet slider
  	#qode-mds-tablet-slider {
		position: relative;

		.qode-mds-tablet-slides {
			position: absolute;
			top: 15%;
			left: 15.5%;
			height: 70%;
			width: 66.5%;
			overflow: hidden;
		}
  	}

  	//phone slider
  	.qode-mds-phone-slider-holder {
		.qode-mds-device-holder-inner {
			position: relative;
			z-index: 50;

			&:before {
				content:'';
				position: absolute;
				height: 12px;
				width: 70%;
				top: 2.2%;
				left: 8%;
				background-image: url('img/mds-phone-top.png');
				background-position: center;
				background-repeat: no-repeat;
	  			background-size: cover;
				z-index: 30;
				pointer-events: none;
			}
		}
  	}

  	#qode-mds-phone-slider {
		position: relative;

		.qode-mds-phone-slides {
			position: absolute;
			top: 3%;
			left: 7.7%;
			height: 87.3%;
			width: 72.5%;
			border-radius: 12px;
			overflow: hidden;
			z-index: 10; //overflow fix
		}
	}

  	//animations
  	@include keyframes(qodeMDSFadeInActive) {
		0% { 
	 		opacity: 0;
		}

		100% {
	  		opacity: 1;
		}
  	}

  	@include keyframes(qodeMDSFadeOutActive) {
		0% { 
	 	 	opacity: 1;
		}

		100% {
	  		opacity: 0;
		}
 	}

  	@include keyframes(qodeMDSSlideInActive) {
		0% { 
		  	@include qodeTransform(translate3d(100%,0,0));
		}

		100% {
	  		@include qodeTransform(translate3d(0,0,0));
		}
  	}

  	@include keyframes(qodeMDSSlideOutActive) {
		0% { 
	  		@include qodeTransform(translate3d(0,0,0));
		}

		100% {
	  		@include qodeTransform(translate3d(-100%,0,0));
		}
  	}

  	//not using 3d transform due to safari blink issue
  	@include keyframes(qodeMDSPhoneSlideInActive) {
		0% { 
		  	@include qodeTransform(translate(0,100%));
		}

		100% {
	  		@include qodeTransform(translate(0,0));
		}
  	}

  	@include keyframes(qodeMDSPhoneSlideOutActive) {
		0% { 
	  		@include qodeTransform(translate3d(0,0,0));
		}

		100% {
	  		@include qodeTransform(translate3d(0,-100%,0));
		}
  	}

  	&.qode-mds-animating {
		.qode-mds-laptop-slide {
	  		&.qode-active:not(.qode-mds-no-animation)  {
				@include qodeAnimation(qodeMDSFadeInActive 1s cubic-bezier(0.694, 0, 0.335, 1) 1 forwards);
	  		}

		  	&.qode-remove {
				@include qodeAnimation(qodeMDSFadeOutActive 1s cubic-bezier(0.694, 0, 0.335, 1) 1 forwards);
	  		}
		}

		.qode-mds-tablet-slide {
	  		&.qode-active:not(.qode-mds-no-animation) {
				@include qodeAnimation(qodeMDSSlideInActive 1s cubic-bezier(0.694, 0, 0.335, 1) 1 forwards);
	 	 	}

	 		&.qode-remove {
				@include qodeAnimation(qodeMDSSlideOutActive 1s cubic-bezier(0.694, 0, 0.335, 1) 1 forwards);
	  		}
		}

		.qode-mds-phone-slide {
	  		&.qode-active:not(.qode-mds-no-animation) {
				@include qodeAnimation(qodeMDSPhoneSlideInActive 1s cubic-bezier(0.694, 0, 0.335, 1) 1 forwards);
	 	 	}

	 		&.qode-remove {
				@include qodeAnimation(qodeMDSPhoneSlideOutActive 1s cubic-bezier(0.694, 0, 0.335, 1) 1 forwards);
	  		}
		}
  	}

  	//additional images 
  	.qode-mds-laptop-image {
  		position: absolute;
  		top: 8.7%;
  		left: 16.5%;
  		height: 69%;
  		width: 67.5%;
  	}

  	.qode-mds-tablet-portrait-image {
  		position: absolute;
  		top: 15%;
  		left: 15.5%;
  		height: 70%;
  		width: 66.5%;
  	}

  	.qode-mds-phone-portrait-holder {
  		.qode-mds-device-holder-inner {
  			position: relative;

  			&:before {
  				content:'';
  				position: absolute;
  				height: 12px;
  				width: 70%;
  				top: 2.2%;
  				left: 8%;
  				background-image: url('img/mds-phone-top.png');
  				background-position: center;
  				background-repeat: no-repeat;
  				background-size: cover;
  				z-index: 30;
  				pointer-events: none;
  			}
  		}

	  	.qode-mds-phone-portrait-image {
	  		position: absolute;
	  		top: 3%;
	  		left: 7.7%;
	  		height: 87.3%;
	  		width: 72.5%;
	  		border-radius: 12px;
		}
  	}

	.qode-mds-tablet-landscape-image {
		position: absolute;
	    top: 18%;
	    left: 15.5%;
	    height: 66.8%;
	    width: 70%;
	}

	@at-root .qode-remove-mds {
		opacity: 0;
		@include qodeTransition(opacity 1s);
	}

	//appear fx 
	&.qode-mds-appear-effect {
		.qode-mds-device-holder {
			visibility: hidden;

			.qode-mds-device-holder-inner {
				opacity: 0;
				@include qodeTransform(translate3d(100px, 0, 0));
				will-change: transform, opacity;
			}

			@for $i from 1 to 11 {
			  	&:nth-child(#{$i}) .qode-mds-device-holder-inner { 
					-webkit-transition: -webkit-transform 1.2s cubic-bezier(0.09, 0.67, 0.22, 0.94) $i * 0.06s, opacity 1s $i * 0.06s;
					transition: transform 1.2s cubic-bezier(0.09, 0.67, 0.22, 0.94) $i * 0.06s, opacity 1s $i * 0.06s;
			  	}
			}
		}

		.qode-mds-content-holder { 
			visibility: hidden;

			> div {
				opacity: 0;
				@include qodeTransition(opacity .6s cubic-bezier(0.694, 0, 0.335, 1));
				will-change: opacity;
			}
		}

		.qode-mds-subtitle-holder {
			.qode-mds-subtitle-holder-inner {
				-webkit-transition-delay: .3s;
				transition-delay: .3s;
			}
		}

		.qode-mds-button-holder {
			.qode-mds-button-holder-inner {
				-webkit-transition-delay: .6s;
				transition-delay: .6s;
			}
		}

		.touchevents & {
			.qode-mds-content-holder { 
				visibility: visible;

				> div {
					opacity: 1;
					@include qodeTransition(none);
				}
			}

			.qode-mds-device-holder {
				visibility: visible;

				.qode-mds-device-holder-inner {
					opacity: 1;
					@include qodeTransform(translate3d(0, 0, 0));
					@include qodeTransition(none);
				}
			}
		}

		#qode-mds-spinner {
			position: absolute;
			top: 50vh;
			left: 50vw;
			display: flex;
			align-items: center;
			justify-content: center;

			.qode-mds-pulse {
			    width: 32px;
			    height: 32px;
			    margin: -16px 0px 0px -16px;
			    background-color: $first-main-color;
			    border-radius: 16px;
			    @include qodeAnimation(scaleout 1.0s infinite cubic-bezier(0.09, 0.67, 0.22, 0.94));
			}
		}

		&.qode-mds-loading {
			.qode-mds-content-holder {
				visibility: visible;

				&.qode-show-item {
					> div {
						opacity: 1;
					}
				}
			}

			.qode-mds-device-holder {
				visibility: visible;

				&.qode-show-device {
				  	.qode-mds-device-holder-inner { 
				  		opacity: 1;
				  		@include qodeTransform(translate3d(0, 0, 0));
				  	}
				}
			}
		}
	}
}
/* ==========================================================================
   Multi-Device Showcase end styles
   ========================================================================== */
