/* Portfolio Masonry with space styles
   ========================================================================== */

.projects_masonry_holder.portfolio_masonry_gallery_with_space {

	margin: 0 -15px;

	.portfolio_masonry_item {
		box-sizing: border-box;
		padding: 15px;

		.image_holder {
			position: relative;
			width: 100%;
			height: 100%;

			img {
				max-height: 100%;
			}
		}

		&.large_height {
			.image_holder {

				overflow: hidden;

				img {
					height: 100%;
					max-width: inherit;
					width: auto;
				}
			}
		}
	}
}

/* Portfolio Masonry(Pinterest) with space styles
   ========================================================================== */

.masonry_with_space .projects_holder{
    position: relative;
    opacity: 0;
}

.masonry_with_space .projects_holder .mix{
    display: block;
    padding: 0px 7px;
    margin: 0px 0px 14px 0px !important;
    -webkit-transition: none;
    -moz-transition: none;
    -o-transition: none;
    transition: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    visibility: visible;
}

.masonry_with_space {
	.projects_holder {
		&.v6 {
			.qode-portfolio-masonry-gallery-grid-sizer,
			.mix {
				width: 16.66%;
			}
		}
		&.v5 {
			.qode-portfolio-masonry-gallery-grid-sizer,
			.mix {
				width: 20%;
			}
		}
		&.v4 {
			.qode-portfolio-masonry-gallery-grid-sizer,
			.mix {
				width: 25%;
			}
		}
		&.v3 {
			.qode-portfolio-masonry-gallery-grid-sizer,
			.mix {
				width: 33.33%;
			}
		}
		&.v2 {
			.qode-portfolio-masonry-gallery-grid-sizer,
			.mix {
				width: 49.99%;
			}
		}
		&.v1 {
			.qode-portfolio-masonry-gallery-grid-sizer,
			.mix {
				width: 100%;
			}
		}
	}
}


