   ========================================================================== */
.qode-horizontal-marquee {
    position: relative;
    overflow: hidden;

    .qode-horizontal-marquee-inner {
        position: absolute;
        height: 100%;
        top: 0;
        left: 0;

        .qode-hm-item {
            position: relative;
            float: left;
            height: 100%;

            &:last-child {
                margin-right: 0 !important;
            }

            .qode-hm-item-inner {
                position: absolute;
                width: 100%;
                left: 0;

                &.qode-top-aligned {
                    top: 0;
                }

                &.qode-bottom-aligned {
                    bottom: 0;
                }

                &.qode-middle-aligned {
                    top: 50%;
                    -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
                }
            }
        }
    }

    &:not(.qode-loop) {
        .qode-horizontal-marquee-inner {
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            cursor: e-resize;
            cursor: -webkit-grab;
            cursor: grab;
            
            &.qode-dragged {
                cursor: -webkit-grabbing;
                cursor: grabbing;
            }
        }
    }

    &.qode-loop {
        visibility: hidden;
        
        .touchevents &,
        .no-touchevents & {
            visibility: visible;
        }

        .no-touchevents &.qode-appear-fx {
            .qode-hm-item-inner > div {
