/* ==========================================================================
   Preview Slider shortcode - start
   ========================================================================== */
.qode-preview-slider {
    position: relative;
    width: 920px;
    max-width: 90%;
    margin: 0 auto;
    padding-bottom: 60px; /* Must match bottom property of .qode-presl-main-slider > .flex-control-nav */

    img {
        width: 100%;
        display: block;
        visibility: hidden;

        &.visible {
            visibility: visible;
        }
    }

    .qode-presl-link {
        position: relative;
        display: block;

        &:after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: white;
            opacity: 0;
            -webkit-transition: all .3s ease;
            transition: all .3s ease;        
        }

        &:hover:after {
            opacity: 0.05;
        }
    }

    .qode-presl-main-slider {
        position: relative;

        .qode-presl-main-item {
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            padding: 0 6.5%;
        }

        .flex-viewport {
            overflow: visible !important;
        }

        .flex-direction-nav {
            opacity: 1;
            -webkit-transition: opacity .3s ease;
            transition: opacity .3s ease;

            &.moving {
                opacity: 0;
                -webkit-transition: none;
                transition: none;
            }

            a {
                width: 54px;
                height: 54px;
                display: block;
                position: absolute;
                top: 50%;
                margin-top: -27px;
                z-index: 10;
                border: none;

                &.flex-prev {
                    margin-left: -27px;
                }


                &.flex-next {
                    right: 0;
                    margin-right: -27px;
                }

                i {
                    line-height: 54px;
                    color: #303030;
                    font-size: 54px;
                }
            }
        }

        .flex-control-nav {
            position: absolute;
            bottom: -60px; /* Must match padding-bottom of .qode-preview-slider */
            left: 50%;
            -webkit-transform: translateX(-50%);
            transform: translateX(-50%);

            li {
                float: left;
                width: 9px;
                height: 9px;
                margin: 0 5px;

                a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    text-indent: -9999px;
                    background-color: #cfcfcf;
                    border-radius: 50%;
                    -webkit-transition: background-color .6s ease;
                    transition: background-color .6s ease;

                    &.flex-active {
                        background-color: #303030;
                    }
                }
            }
        }

        .slides {
            li.clone_end{
                position: absolute;
                top: 0;
                left: 50%;
            }

            li.clone_start{
                position: absolute;
                top: 0;
                right: 100%;
            }
        }
    }

    .qode-presl-small-slider-holder {
        position: absolute;
        width: 32.6%;
        bottom: 0;
        right: 4%;

        .qode-presl-small-slider {
            position: absolute;
            top: 11.3%;
            left: 24%;
            width: 52%;
            z-index: 1;

            .flex-direction-nav {
                display: none !important;
            }

            .flex-control-nav {
                display: none !important;
            }
        }

        .qode-presl-phone {
            display: block;
            z-index: 2;
        }
    }
}

/* ==========================================================================
   Preview Slider shortcode - end
   ========================================================================== */

/* ==========================================================================
   In-Device Slider shortcode - start
   ========================================================================== */
.qode-in-device-slider {
    position: relative;
