.blog_holder.blog_masonry_date_in_image.qodef-zoom article:not(.format-gallery) {
    > .post_image {
    overflow: hidden;
        a {
            @include qodeTransition(all .2s ease);
            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background-color: rgba(0,0,0,.3);
                opacity: 0;
                @include qodeTransition(opacity .2s ease);
            }
        }
    }
    &:hover {
        > .post_image {
            a {
                @include qodeTransform(scale(1.1));
                -webkit-transition: -webkit-transform .35s cubic-bezier(0.3, 0.67, 0.24, 0.86);
                transition: transform .35s cubic-bezier(0.3, 0.67, 0.24, 0.86);
                &:after {
                    opacity: 1;
                    @include qodeTransition(opacity .6s cubic-bezier(0.3, 0.67, 0.24, 0.86));
                }
            }
        }
    }
}

.blog_masonry_date_in_image.qodef-zoom article.format-gallery {
    ul.slides li {
        overflow: hidden;
        @include qodeTransition(all .2s ease);
        a:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: rgba(0,0,0,.3);
            z-index: 10;
            opacity: 0;
            @include qodeTransition(opacity .3s ease);
        }
        &:hover {
            @include qodeTransform(scale(1.1));
            -webkit-transition: -webkit-transform .35s cubic-bezier(.3,.67,.24,.86);
            transition: transform .35s cubic-bezier(.3,.67,.24,.86);
