/* Qode Clients Switch Images - Roll style */


.qode_clients{

    &.qode_clients_switch_images{

        &.qode_clients_switch_roll{

            .qode_client_image_holder{

                position: relative;
                overflow: hidden;

                a{

                    .qode_client_main_image{
                        @include qodeTransition(transform 0.4s ease-in-out);
                    }

                    .qode_client_hover_image{
                        position: absolute;
                        left: 50%;
                        @include qodeTransform(translate(-50%, -100%));
                        @include qodeTransition(transform 0.4s ease-in-out);
                    }

                    &:hover{

                        opacity: 1;

                        .qode_client_main_image{
                            @include qodeTransform(translateY(100%));
                        }

                        .qode_client_hover_image{
                            @include qodeTransform(translate(-50%, 0));
                        }

                    }
                }
            }
        }
    }
}


/* Qode Clients Switch Images - Fade style */


.qode_clients{

    &.qode_clients_switch_images{

        &.qode_clients_switch_fade{

            .qode_client_image_holder{

                position: relative;
                overflow: hidden;

                a{

                    .qode_client_main_image{
                        @include qodeTransition(opacity 0.6s ease-in-out);
                    }

                    .qode_client_hover_image{
                        position: absolute;
                        left: 50%;
                        @include qodeTransform(translate(-50%));
                        opacity: 0;
                        @include qodeTransition(opacity 0.6s ease-in-out);
                    }

                    &:hover{

                        opacity: 1;

                        .qode_client_main_image{
                            opacity: 0;
                        }

                        .qode_client_hover_image{
                            opacity: 1;
                        }

                    }
                }
            }
        }
    }
}

