//popup menu svg opener/closer
.popup_menu{
    &.qode-popup-menu-svg-opener{
        display: inline-block;
        position: relative;
        width: auto;

        .qode-popup-menu-opener{
            transition: all 0.3s ease;

            svg{
                *{

                    fill: currentColor;
                    stroke: currentColor;

                    .light & {
                        fill: #fff;
                        stroke: #fff;
                    }

                    .dark & {
                        fill: #000;
                        stroke: #000;
                    }
                }
            }
        }

        .qode-popup-menu-closer{
            position: absolute;
            left: calc(50% + 6px);
            transform: translateX(-50%);
            top: 0;
            visibility: hidden;
            opacity: 0;
            transition: all 0.3s ease;

            svg{
                *{
                    fill: currentColor;
                    stroke: currentColor;

                    .light & {
                        fill: #fff;
                        stroke: #fff;
                    }

                    .dark & {
                        fill: #000;
                        stroke: #000;
                    }
                }
            }
        }

        &.opened{
            .qode-popup-menu-opener{
                visibility: hidden;
                opacity: 0;
            }

            .qode-popup-menu-closer{
                visibility: visible;
                opacity: 1;
            }
        }
    }
}

