    margin-right: auto;
    margin-left: auto;
    padding: 0px 40px;
    background-color: #ffffff;
}

.transparent_content.overlapping_content .content .content_inner > .container,
.transparent_content.overlapping_content .content .content_inner > .full_width > .full_width_inner{ /* full_width_inner because of the negative margin on it */
    background-color: #ffffff;
}

.overlapping_content .content .content_inner > .container > .overlapping_content .overlapping_content_inner{
    overflow: hidden;
    text-align: left;
}

.overlapping_content_margin{
    margin: 0px -40px;
    display: block;
    position: relative;
}

@media only screen and (max-width: 1200px) {
    .overlapping_content .content .content_inner > .container > .overlapping_content{
        padding: 0px 20px !important;
    }
    .overlapping_content_margin {
        margin: 0px -20px !important;
    }
}

@media only screen and (max-width: 1000px) {
    header .overlapping_content_margin {
        margin: 0px !important;
    }
}

/* ==========================================================================
   End of overlapping content
   ========================================================================== */

/* ==========================================================================
   Start blog with next post on bottom
   ========================================================================== */

.blog_vertical_loop .full_width_inner{
    margin: 0px !important;
}

.blog_vertical_loop article{
    position: relative;
    vertical-align: middle;
    z-index: 1; /*one after another overlapping*/
    margin: 0px 0px 140px 0px;
}

.blog_vertical_loop article.move_up{
    -webkit-transition: transform 450ms ease 0s;
    transition: transform 450ms ease 0s;
}

.blog_vertical_loop article.fade_out {
    opacity: 0;
    transform: scale(0.8) translate3d(0px, -10%, 0px);
    transition: all 450ms ease 0s;
}

.blog_vertical_loop article .post_image{
    margin-bottom: 55px !important;
    height: 400px;
}

.blog_vertical_loop article .post_image_inner{
    height: 100%;
    overflow: hidden;
}

.blog_vertical_loop article .post_image_inner a{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.blog_vertical_loop article.previous_post{
    position: absolute;
    opacity: 0;
    transform: scale(0.8) translate3d(0px, -10%, 0px);
}

.blog_vertical_loop article.fade_in {
    transition: all 450ms ease 0s;
    opacity: 1;
    transform: scale(1) translate3d(0px, 0px, 0px);
}

.blog_vertical_loop article.next_post,
.blog_vertical_loop article.next_post .post_content_holder .post_image{
    margin: 0px !important;
}

.blog_vertical_loop article.next_post .post_content_holder > .grid_section,
.blog_vertical_loop article.next_post .post_content_holder .post_image > .grid_section{
    display: none;
}

.blog_vertical_loop .blog_load_next{
    display: none;
    position: absolute;
    left: 0;
    top: -30px;
    width: 100%;
}

.blog_vertical_loop article.next_post .blog_load_next{
    display: block;
}

.blog_vertical_loop .blog_load_prev{
    display: block;
    position: absolute;
    left: 0;
    bottom: -30px;
    width: 100%;
}

.blog_vertical_loop article.next_post .blog_load_prev{
    display: none;
}

.blog_vertical_loop_button{
    text-align: left;
}

.blog_vertical_loop_back_button{
    text-align: right;
}

.blog_vertical_loop_button .button_icon a,
.blog_vertical_loop_back_button .button_icon a{
    width: 60px;
    height: 60px;
    background-color: #303030;
    display: inline-block;
    position: relative;
    text-align: center;
    border-radius: 50%;
    -webkit-transition: background-color 0.15s ease-out;
    -moz-transition: background-color 0.15s ease-out;
    -ms-transition: background-color 0.15s ease-out;
    -o-transition: background-color 0.15s ease-out;
    transition: background-color 0.15s ease-out;
}

.blog_vertical_loop_button .button_icon a:before,
.blog_vertical_loop_back_button .button_icon a:before{
    content: '3';
    font-family: "ElegantIcons";
    font-variant: normal;
    font-weight: normal;
    line-height: 60px;
    text-transform: none;
    color: #ffffff;
    font-size: 22px;
    -webkit-transition: color 0.15s ease-out;
    -moz-transition: color 0.15s ease-out;
    -ms-transition: color 0.15s ease-out;
    -o-transition: color 0.15s ease-out;
    transition: color 0.15s ease-out;
}

.blog_vertical_loop_back_button .button_icon a:before{
    content: 'J';
}

.blog_vertical_loop_button_holder .last_page{
    display: none;
}

.blog_vertical_loop article .post_image_title{
    opacity: 0;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: table;
    -webkit-transition: all 0.5s ease 0s ;
    transition: all 0.5s ease 0s;
}

.blog_vertical_loop article.next_post .post_image_title{
    opacity: 1;
}

.blog_vertical_loop article .post_image_title .post_image_title_inner{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    background-color: rgba(0,0,0,0.6);
}

.blog_vertical_loop article .post_image_title .post_image_title_inner h2{
    color: #ffffff;
}

.blog_holder.blog_vertical_loop_type{
    position: relative;
}

.blog_holder.blog_vertical_loop_type article:not(.format-quote):not(.format-link) .social_share_list_holder{
    margin-top: 15px;
}

.blog_holder.blog_vertical_loop_type .qbutton.loop_more{
    margin: 40px 0px 0px 0px;
}
.blog_holder.blog_vertical_loop_type article:not(.format-quote):not(.format-link) .post_text .post_text_inner{
    background-color: transparent;
    padding: 0;
}
.blog_holder.blog_vertical_loop_type article.format-quote .post_text,
.blog_holder.blog_vertical_loop_type article.format-link .post_text{
    margin-bottom: 30px;
}
.blog_holder.blog_vertical_loop_type article .post_text h2{
    margin-bottom: 35px;
}

.blog_holder article.blog_vertical_loop_type .post_info{
    margin-bottom:8px;
}

.blog_holder.blog_vertical_loop_type article .post_info > span,
.blog_holder.blog_vertical_loop_type article .post_info > div {
    padding-right: 20px;
    position: relative;
    float: left;
}
.blog_holder.blog_vertical_loop_type article .post_info > span:last-child,
.blog_holder.blog_vertical_loop_type article .post_info > div:last-child{
    padding-right: 0px;
}

.blog_holder.blog_vertical_loop_type article .post_info > span:after,
.blog_holder.blog_vertical_loop_type article .post_info > div:after {
    position: absolute;
    right: 5px;
    top: 0;
    content: "/";
}

.blog_holder.blog_vertical_loop_type article .post_info > span:last-child:after,
.blog_holder.blog_vertical_loop_type article .post_info > div:last-child:after {
    content: "";
}

.blog_holder.blog_vertical_loop_type .blog_like a {
    line-height: inherit;
}
/* ==========================================================================
   End blog with next post on bottom
   ========================================================================== */

