============================================================================ */

.qode_parallax_layers{
    width: 100%;
    height: 500px;
    position: relative;
    overflow: hidden;
}

.qode_parallax_layers_holder{
    position: relative;
    width: 110%;
    height: 110%;
    top: -5%;
    left: -5%;
}

.qode_parallax_layers_holder .image{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
