}

.qode_parallax_layers_holder .paralax_layers_content_holder{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}

.qode_parallax_layers_holder .paralax_layers_content{
    display: table;
    width: 100%;
    height: 100%;
}

.qode_parallax_layers_holder .paralax_layers_content_inner{
