.qode-instagram-feed {
    margin-left: -5px;
    margin-right: -5px;
    list-style: none;
    &.qodef-scale-with-light-overlay{
        a {
            display: block;
            position: relative;
            overflow: hidden;
            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background-color: rgba(255,255,255,.15);
                opacity: 0;
                -webkit-transition: opacity .4s ease;
                transition: opacity .4s ease;
            }
            &:hover {
                &:after {
                    opacity: 1;
                }
                img {
                   -webkit-transform: scale(1.05);
                   transform: scale(1.05);
                }
            }
            img {
                -webkit-transition: -webkit-transform .4s ease;
                transition: transform .4s ease;
            }

        }
    }
    &.qodef-scale-with-dark-overlay{
        a {
            display: block;
            position: relative;
            overflow: hidden;
            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background-color: rgba(0,0,0,.35);
                opacity: 0;
                -webkit-transition: opacity .4s ease;
                transition: opacity .4s ease;
            }
            &:hover {
                &:after {
                    opacity: 1;
                }
                img {
                   -webkit-transform: scale(1.05);
                   transform: scale(1.05);
                }
            }
            img {
                -webkit-transition: -webkit-transform .4s ease;
                transition: transform .4s ease;
            }

        }
    }
}

