.search-results {

    .blog_holder{

        $gallery_columns: ('two', 'three', 'four', 'five', 'six');
        @for $i from 0 to length($gallery_columns) {
            &.qode-search-results-#{nth($gallery_columns, $i+1)}-columns {
                $column_width: 100%/($i+2);

                article {
                    float: left;
                    width: $column_width;

                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        &:nth-child(#{$i+2}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }

        @include laptop-landscape-large {

            $gallery_columns: ('six');

            @for $i from 0 to length($gallery_columns) {
                &.qode-search-results-#{nth($gallery_columns,$i+1)}-columns {
                    $column_width: 100%/5;

                    article {
                        width: $column_width;

                        @media only screen and (min-width: $laptop-landscape-medium-plus-pixel) {
                            &:nth-child(6n+1) {
                                clear: none;
                            }

                            &:nth-child(5n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }

        @include laptop-landscape-medium {

            $gallery_columns: ('five', 'six');

            @for $i from 0 to length($gallery_columns) {
                &.qode-search-results-#{nth($gallery_columns,$i+1)}-columns {
                    $column_width: 100%/4;

                    article {
                        width: $column_width;
                        $gallery_list_columns_label: #{nth($gallery_columns,$i+1)};

                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            @if $gallery_list_columns_label == "five" {
                                &:nth-child(5n+1) {
                                    clear: none;
                                }
                            } @else if $gallery_list_columns_label == "six" {
                                &:nth-child(6n+1) {
                                    clear: none;
                                }
                            }

                            &:nth-child(4n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }

        @include ipad-landscape {

            $gallery_columns: ('five', 'six');

            @for $i from 0 to length($gallery_columns) {
                &.qode-search-results-#{nth($gallery_columns,$i+1)}-columns {
                    $column_width: 100%/3;

                    article {
                        width: $column_width;

                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            &:nth-child(3n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }

            $gallery_columns_two: ('four');

            @for $i from 0 to length($gallery_columns_two) {
                &.qode-search-results-#{nth($gallery_columns_two,$i+1)}-columns {
                    $column_width: 100%/2;

                    article {
                        width: $column_width;
                    }
                }
            }

            $gallery_columns_clear: ('two', 'three', 'four');

            @for $i from 0 to length($gallery_columns_clear) {
                &.qode-search-results-#{nth($gallery_columns_clear,$i+1)}-columns {

                    article {

                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }

        @include ipad-portrait {

            $gallery_columns: ('five', 'six');

            @for $i from 0 to length($gallery_columns) {
                &.qode-search-results-#{nth($gallery_columns,$i+1)}-columns {
                    $column_width: 100%/2;

                    article {
                        width: $column_width;

                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            &:nth-child(2n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }

            $gallery_columns_two: ('three');

            @for $i from 0 to length($gallery_columns_two) {
                &.qode-search-results-#{nth($gallery_columns_two,$i+1)}-columns {

                    article {
                        width: 100%;
                    }
                }
            }
        }

        @include phone-landscape {

            $gallery_columns: ('two', 'three', 'four', 'five', 'six');

            @for $i from 0 to length($gallery_columns) {
                &.qode-search-results-#{nth($gallery_columns,$i+1)}-columns {

                    article {
                        width: 100%;
                    }
                }
            }
        }

        $columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large', 'huge' );
        $columns_space_width: (0, 5, 10, 15, 20, 25, 40);

        @for $i from 0 to length($columns_space_label) {
            &.qode-#{nth($columns_space_label,$i+1)}-space {
                $column_width: nth($columns_space_width,$i+1);
                $column_width_margin: $column_width * 2;

                .qode-outer-space {
                    article {
                        box-sizing: border-box;
                        padding: 0 #{$column_width}px;
                        margin: 0 0 #{$column_width_margin}px;
                    }
                }

                &.qode-disable-item-bottom-space {
                    article {
                        margin: 0;
                    }
                }
            }
        }

    }

}

