/* ==========================================================================
   Separator Widget Start
   ========================================================================== */
.qode_separator_widget {
    &.qode-disabled-below--1024 {
        @include ipad-landscape {
            display: none;
        }
    }
    
    &.qode-disabled-below--768 {
        @include ipad-portrait {
            display: none;
        }
    }
    
    &.qode-disabled-below--680 {
        @include phone-landscape {
            display: none;
        }
    }
    
    &.qode-disabled-below--480 {
        @include phone-portrait {
            display: none;
        }
    }
}
/* ==========================================================================
   Separator Widget End
   ========================================================================== */

