/* ==========================================================================
   #Numbered Process style - start
   ========================================================================== */
.qode-numbered-process-holder{
	position: relative;

	.qode-numbered-process-inner{
		font-size: 0;
	}

	.qode-np-item{
		display: inline-block;
		vertical-align: top;
		text-align: center;
		box-sizing: border-box;
	}

	.qode-np-title{
		margin-top: 33px;
	}

	.qode-np-item-image-holder{
		position: relative;
		height: 0;
		padding-top: 100%;
		@include qodeBorderRadius(50%);

		.qode-np-item-image-inner{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			border: 12px solid #fff;
			@include qodeBorderRadius(50%);
		}

		.qode-np-item-image-table{
			display: table;
			width: 100%;
			height: 100%;
			table-layout: fixed;
		}

		.qode-np-item-image-table-cell{
			display: table-cell;
			width: 100%;
			vertical-align: middle;

			img{
				@include qodeBorderRadius(50%);
				vertical-align: middle;
			}
		}

		.qode-np-item-number{
			position: absolute;
			top: 13%;
			left: 88%;
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			font-size: 20px;
			color: #000;
			background-color: $first-main-color;
			@include qodeBorderRadius(50%);
			@include qodeTransform(translate(-50%,-50%));
		}

		.qode-np-title{
			position: absolute;
			top: 50%;
			left: 50%;
			width: 65%;
			color: #fff;
			margin: 0;
			@include qodeTransform(translate(-50%,-50%));
		}
	}

	&.qode-numbered-process-holder-items-three{

		.qode-np-item{
			width: 33.33%;

			&:nth-child(3) .qode-np-line{
				border: none;
			}
		}
	}

	&.qode-numbered-process-holder-items-four{

		.qode-np-item{
			width: 25%;

			&:nth-child(4) .qode-np-line{
				border: none;
			}
		}
	}

	&.qode-numbered-process-holder-items-five{

		.qode-np-item{
			width: 20%;

			&:nth-child(5) .qode-np-line{
				border: none;
			}
		}
	}

	.qode-np-line{
		display: inline-block;
		position: absolute;
		top: 50%;
		left: 100%;
		height: 0;
		border-bottom-color: #c3c3c3;
		border-bottom-width: 1px;
		@include qodeTransform(translateY(-50%));
	}

	&.qodef-np-padding-small{

		.qode-numbered-process-inner{
			margin: 0 -30px;
		}

		.qode-np-item{
			padding: 0 30px;
		}

		.qode-np-line{
			width: 60px;
		}
	}

	&.qodef-np-padding-medium{

		.qode-numbered-process-inner{
			margin: 0 -50px;
		}

		.qode-np-item{
			padding: 0 50px;
		}

		.qode-np-line{
			width: 100px;
		}
	}

	&.qodef-np-padding-large{

		.qode-numbered-process-inner{
			margin: 0 -70px;
		}

		.qode-np-item{
			padding: 0 70px;
		}

		.qode-np-line{
			width: 140px;
		}
	}

	&.qode-np-line-solid{

		.qode-np-line{
			border-bottom-style: solid;
		}
	}

	&.qode-np-line-dashed{

		.qode-np-line{
			border-bottom-style: dashed;
		}
	}

	&.qode-np-line-skin-dark{

		.qode-np-line{
			border-bottom-color: #000;
		}
	}

	&.qode-np-line-skin-light{

		.qode-np-line{
			border-bottom-color: #fff;
		}
	}
}
/* ==========================================================================
   #Numbered Process style - end
   ========================================================================== */