/* ==========================================================================
   #Icon Helper style - start

   #Please note that this is not icon shortcode css,
    this is improved css which is possible to use in new shortcodes
   ========================================================================== */
.qode-icon-holder{
	$transition-duration: 0.8s;

	display: inline-block;
	vertical-align: middle;

	&.qode-icon-circle,
	&.qode-icon-square {
		background-color: $first-main-color;
		width: 2em;
		height: 2em;
		line-height: 2em;
		text-align: center;

		@include qodeTransition(
				background-color $transition-duration $aux-easing-1,
				border-color $transition-duration $aux-easing-1
		);

		a {
			display: inline-block;
			width: 100%;
			height: 100%;
		}

	}

	&.qode-icon-circle {
		border-radius: 50%;
	}

	&.qode-icon-tiny {
		font-size: 1.33333333em;
		line-height: .75em;
		vertical-align: -15%;
	}

	&.qode-icon-small {
		font-size: 2em;
	}

	&.qode-icon-medium {
		font-size: 3em;
	}

	&.qode-icon-large {
		font-size: 4em;;
	}

	&.qode-icon-huge {
		font-size: 5em;
	}

	.qode-icon-element {
		line-height: inherit;
		color: #fff;
		@include qodeTransition(
				color $transition-duration $aux-easing-1
		);

		&:before {
			line-height: inherit;
		}
	}

}
/* ==========================================================================
   #Icon Helper style - end
   ========================================================================== */