/* Inter Page Navigation - begin
========================================================================== */

.qode-inter-page-navigation-holder{
	position: relative;
	padding: 80px 41px;
	background-color: #fff;
	z-index: 100;

	.qode-inter-page-navigation-inner {
		display: table;
		width: 100%;
		table-layout: fixed;
	}

	.qode-inter-page-navigation-prev,
	.qode-inter-page-navigation-next {
		display: table-cell;
		vertical-align: middle;

		a {
			vertical-align: middle;
			color: #303030;
		}

		.qode-ipn-icon {
			color: $first-main-color;
		}
	}

	.qode-inter-page-navigation-next {
		text-align: right;

		.qode-ipn-arrow {
			padding-left: 21px;
			padding-right: 0;
		}
	}

	.qode-ipn-arrow {
		font-size: 56px;
		display: table-cell;
		width: 1%;
		vertical-align: middle;
		padding-right: 21px;
	}

	.qode-inter-page-title {
		display: table-cell;
		vertical-align: middle;

		span {
			font-size: 12px;
			font-weight: 500;
		}

	}

	.qode-inter-page-navigation-back-link {
		display: table-cell;
		vertical-align: middle;
		width: 84px;
	}
	.qode-inter-page-navigation-back-link-inner {
		position: relative;
		width: 84px;
		height: 84px;
		line-height: 84px;
		border-radius: 84px;
		text-align: center;
		background-color: $first-main-color;

		a {
			display: block;
			position: relative;
			z-index: 2;
			color: #303030;
			font-size: 12px;
			font-weight: 500;
		}

		&:after {
			content: '';
			position: absolute;
			top: 2px;
			left: 2px;
			width: calc(100% - 4px);
			height: calc(100% - 4px);
			border-radius: 100%;
			background: #fff;
		}
	}

	.container {
		background-color: transparent !important;
	}

}


/* Inter Page Navigation - end
========================================================================== */