/* ==========================================================================
Call To Action Section shortcode styles - start
========================================================================== */
.qode-cta-section {
	position: relative;

	.qode-cta-section-text-wrapper {
		position: absolute;
		display: table;
		table-layout: fixed;
		top:50%;
		left: 50%;
		@include qodeTransform(translate(-50%,-50%));
		text-align: center;
		width: 600px;
		height: 600px;

		.qode-cta-section-text-wrapper-inner {
			display: table-cell;
			vertical-align: middle;
			height: 100%;
			width: 100%;
		}

		.qode-cta-section-title {
			font-size: 46px;
			text-transform: none;
			font-weight: 300;
		}

		.qode-cta-section-description-holder {
			padding: 0 20px;
			margin-bottom: 40px;
		}

		.qode-cta-section-description {
			font-size: 17px;
			line-height: 1.68em;
		}
	}

	&.qode-cta-appear-effect {
		img {
			-webkit-transition: -webkit-transform 2s, opacity 2s;
			transition: transform 2s, opacity 2s;

			.no-touchevents & {
				visibility: hidden;
				opacity: 0;
				@include qodeTransform(scale(1.2) translateY(100px));
				-webkit-transform-style: preserve-3d;
				transform-style: preserve-3d;
			}

			&.qode-appeared {
				visibility: visible;
				opacity: 1;
				@include qodeTransform(scale(1) translateY(0px));
				@include animation(qodeCTAScaleUp, 15s, 1, ease, 2s);

				@include keyframes(qodeCTAScaleUp) {
					0% {
						@include qodeTransform(scale(1));
					}

					100% {
						@include qodeTransform(scale(1.07));
					}
				}
			}
		}

		.qode-cta-section-text-wrapper {
			.qode-cta-section-title-holder {
				display: inline-block;
				vertical-align: middle;
				position: relative;
				overflow: hidden;

				&:after {
					content: "";
					position: absolute;
					top: 0%;
					left: 0%;
					width: 50%;
					height: 200%;
					visibility: hidden;
					@include qodeTransform(skew(-25deg) translateX(-200%));
					background: rgba(255, 255, 255, 0.1);
					background: linear-gradient( to right, rgba(255, 255, 255, 0.13) 0%, rgba(255, 255, 255, 0.13) 77%, rgba(255, 255, 255, 0.55) 92%, rgba(255, 255, 255, 0.0) 100% );
				}
			}
			
			.no-touchevents & {
				visibility: hidden;
				opacity: 0;
				@include qodeTransform(translate(-50%,-50%) scale(.5) translateY(100px));
				-webkit-transform-origin: 50% 0;
				transform-origin: 50% 0;
				-webkit-transition: -webkit-transform 1s cubic-bezier(0,0,0,1), opacity .5s .2s; 
				transition: transform 1s cubic-bezier(0,0,0,1), opacity .5s .2s;
			}

			&.qode-appeared {
				visibility: visible;
				opacity: 1;
				@include qodeTransform(translate(-50%,-50%) scale(1) translateY(0));

				.qode-cta-section-title-holder:after {
					visibility: visible;
					@include animation(qodeCTABling, 4s, 1, ease, 1s);

					@include keyframes(qodeCTABling) {
						0% {
							left: 0%;
							@include qodeTransform(skew(-25deg) translateX(-200%));
							width: 50%;
						}

						20% {
							width: 300%;
						}

						100% {
							left: 120%;
							@include qodeTransform(skew(-25deg) translateX(0));
							width: 10%;
						}
					}
				}
			}
		}
	}
}
/* ==========================================================================
Call To Action Section shortcode styles - end
========================================================================== */
